<template>
<div class="songList" :watcher="songList" >
	<div class="song_list_wrapper">
		<ul class="songs" ref="songs">
			<li class="song" v-for="(song, index) in songList" @click="_palyMusic(song, index)">
				<div class="rank" v-show="rank">
					<span class="icon" :class="_rankIconClass(index)" v-text="index"></span>
				</div>
				<div class="song_wrapper">
				<div class="song_name">
					{{song.songName}}
				</div>
				<div class="singer_name">
					{{song.singerName}} - {{song.albumName }}
				</div>
				</div>
			</li>
		</ul>
	</div>
</div>
</template>	
<script type="text/javascript">
import Scroll  from '@/base/scroll/scroll'
import { mapGetters, mapMutations, mapActions} from 'vuex'
import { toggleBottom } from '@/common/js/mixin'

	export default {
		mixins:[toggleBottom],

		props: {
			songList: {
				type: Array
			},
			rank: {
				type: Boolean,
				dafault: false
			}
		},

		computed: {
		...mapGetters([
			'get_current_index'
			])
		},

		created() {
			console.log(this.rank)
		},
		methods: {
		_palyMusic(song, index) {
					song.currentIndex = index
					this.$emit('palyMusic', song)
		},
		_rankIconClass(index) {
			switch (index) {
				case 0:
					return 'icon0'
					break;
				case 1:
					return 'icon1'
					break;
				case 2:
					return 'icon2'
					break;
				default:
				  return 'text'	

			}
		},

		toggleBottom() {
		 	if (this.get_current_index === -1) {
		 		this.$refs.songs.style.paddingBottom = '0px'
		 	}else {
		 		this.$refs.songs.style.paddingBottom = '60px'
		 	}
		}		
		
		}
		
	}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~@/common/stylus/variable.styl'	
  @import '~@/common/stylus/mixin.styl'	
   .songList
   	&::after
   	 content ''
   	 dispaly: block
   	 height: 300px	 
	.song_list_wrapper	
	 padding: 0px 28px
	 .songs
	  .song
	   padding: 20px 0px	
	   display: flex
	   .rank
	   	flex: 0 0 40px
	   	color: $color-theme
	   	line-height: 42px
	   	font-size: $font-size-large
	    .icon0, .icon1, .icon2 
	     display block	
	     bg-image('./first')
	     background-position left center
	     background-size 25px 25px
	     background-repeat no-repeat
	     font-size: 0px
	    .icon1
	     bg-image('./second') 			
	    .icon2 
	     bg-image('./third')
	    .text 
	     margin-left: 4px
	   .singer_name
	   	flex: 1
	   	color: $color-text-d		 	
	    margin-top: 10px
	    
</style>


