<template>
 <div class="banner" ref="banner" >
 	<ul class="slider_warpper" ref="slider_warpper">
 		<li ref="slider_item" class="slider_item" v-for="pic in slider" >
 			<a :href="pic.linkUrl">
 				<img @load="_loaded(pic)"  class="slider_img" :src="pic.picUrl"  >
 			</a>
 		</li>
 	</ul>
 	<div class="dots_wrapper">
 		<span class="dot" :class="{active: index === curPic}" v-for="(dot, index) in dots"></span>
 	</div>
 </div>
</template>	
<script type="text/javascript">
	import BScroll from 'better-scroll'

	const INTERVAL = 4000
	export default {
		props: {
			slider: {
				type: Array
			}
		},
		data() {
			return {
				curPic: 0,
				dots: []
			}
		},
		created() {
			this.firstLoad = false
			window.onresize = () => {
			  this._getSliderItemWidth(true)	
			  this.scroll.refresh()
			}
		},
		mounted() {
			this.$nextTick(()=> {
				this._getSliderItemWidth()
				this._initDots()
				this._initBScroll()
				this._play()
				
			})
			
		},
		activated() {
	        this._play()
	    
	    },
	    deactivated() {
	      clearTimeout(this.timer)
	    },
	    beforeDestroy() {
	      clearTimeout(this.timer)
    },
		methods: {
			_loaded(val) {
				if(this.firstLoad) {
					return 
				}
				this.$emit('picLoaded', val)
				this.firstLoad = true 
			},
			_initBScroll() {
				this.scroll = new BScroll(this.$refs.banner, {
						click: true,
						scrollX: true,
						scrollY: false,
						momentum: false,
					    snap: {
						loop: true,
						threshold: 0.1
					}
				})
				this.scroll.on('scrollEnd', () => {
					this.scrollEnd()
				})

				 this.scroll.on('beforeScrollStart', () => {
		            clearTimeout(this.timer)
		        })
			},
			scrollEnd() {
				this.curPic = this.scroll.getCurrentPage().pageX
				this._play() 	
			},
			_getSliderItemWidth(isresize) {
				
					let picItems = this.$refs.slider_warpper.children
					let picItemsLen = picItems.length  
					let itemOffsetWidth = this.$refs.banner.offsetWidth
					if(!isresize) {
						this.$refs.slider_warpper.style.width = (itemOffsetWidth * (picItemsLen+ 2))+ 'px'
					}else {
						this.$refs.slider_warpper.style.width = (itemOffsetWidth * (picItemsLen))+ 'px'
					}
					

					for(let i = 0; i<picItemsLen; i++ ) {
						
						picItems[i].style.width = itemOffsetWidth + 'px'
					}
						
			},
			_initDots() {
				let picItems = this.$refs.slider_warpper.children
				this.dots = new Array(picItems.length)
			},
			_play() {
				let length = this.$refs.slider_warpper.children.length -2
				this.timer = setTimeout(() => {
					this.curPic++
					if(this.curPic >= length) {
					 this.curPic = 0	
					}
					this.scroll.goToPage(this.curPic, 0, 400)
				}, INTERVAL)
			}
		}
	}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
   .banner
   	position: relative
   	display: block
   	width: 100%
   	overflow: hidden
   	font-size: 0px	 	
 	.slider_warpper
	 font-size: 0px
	 .slider_item	
	  display: inline-block
	  .slider_img
	   width: 100%
	.dots_wrapper
	 position: absolute	
	 left: 50%
	 bottom: 15px
	 transform: translateX(-50%)
	 .dot	   			
	  display: inline-block
	  margin-right: 10px
	  height: 10px
	  width: 10px
	  border-radius: 50%
	  background: #fff
	 .active	
	  width: 20px
	  border-radius: 10px

</style>	



