import * as types from './types'
export default {
	setSongInfo({commit}, newVal) {
		let {singerName, songName, songMid, songId, lyrics, albumName, albumMid, interval, songImg, url,
		 currentIndex, sequenceList, playList, playModel, fullPlay, playStatus} = newVal
		console.log(fullPlay)		 
		commit(types.SET_SINGER_NAME, singerName)
		commit(types.SET_SONG_NAME, songName)
		commit(types.SET_SONG_MID, songMid)
		commit(types.SET_SONG_ID, songId)
		commit(types.SET_LYRICS, lyrics)
		commit(types.SET_ALBUM_NAME, albumName)
		commit(types.SET_ALBUM_MID, albumMid)
		commit(types.SET_INTERVAL, interval)
		commit(types.SET_SONG_IMG, songImg)
		commit(types.SET_URL, url)
		commit(types.SET_CURRENT_INDEX, currentIndex)
		commit(types.SET_SEQUENCE_LIST, sequenceList)
		commit(types.SET_PLAY_LIST, playList)
		// commit(types.SET_PPLAY_MODEL, playModel)
		commit(types.SET_FULL_PLAY, fullPlay)
		commit(types.SET_PLAY_STATUS, playStatus)
		commit(types.SET_CURRENT_TIME, 0)
		console.log(2222222)

	},

	changeList({commit}, newVal) {
	
		let {singerName, songName, songMid, songId, lyrics, albumName, albumMid, interval, songImg, url,
		 currentIndex, fullPlay, playStatus} = newVal
		commit(types.SET_CURRENT_TIME, 0) 
		commit(types.SET_SINGER_NAME, singerName)
		commit(types.SET_SONG_NAME, songName)
		commit(types.SET_SONG_MID, songMid)
		commit(types.SET_SONG_ID, songId)
		commit(types.SET_LYRICS, lyrics)
		commit(types.SET_ALBUM_NAME, albumName)
		commit(types.SET_ALBUM_MID, albumMid)
		commit(types.SET_INTERVAL, interval)
		commit(types.SET_SONG_IMG, songImg)
		commit(types.SET_URL, url)
		commit(types.SET_CURRENT_INDEX, currentIndex)
		commit(types.SET_FULL_PLAY, fullPlay)
		commit(types.SET_PLAY_STATUS, playStatus)
	},

	setQuerySongInfo({commit}, newVal) {
		let {singerName, songname, songmid, songid, lyrics, albumname, albummid, interval, songImg, url,
		 currentIndex, sequenceList, playList, playModel, fullPlay, playStatus} = newVal
		console.log(fullPlay)		 
		commit(types.SET_SINGER_NAME, singerName)
		commit(types.SET_SONG_NAME, songname)
		commit(types.SET_SONG_MID, songmid)
		commit(types.SET_SONG_ID, songid)
		commit(types.SET_LYRICS, lyrics)
		commit(types.SET_ALBUM_NAME, albumname)
		commit(types.SET_ALBUM_MID, albummid)
		commit(types.SET_INTERVAL, interval)
		commit(types.SET_SONG_IMG, songImg)
		commit(types.SET_URL, url)
		commit(types.SET_CURRENT_INDEX, currentIndex)
		commit(types.SET_SEQUENCE_LIST, sequenceList)
		commit(types.SET_PLAY_LIST, playList)
		// commit(types.SET_PPLAY_MODEL, playModel)
		commit(types.SET_FULL_PLAY, fullPlay)
		commit(types.SET_PLAY_STATUS, playStatus)
		commit(types.SET_CURRENT_TIME, 0)
		console.log(2222222)

	},

}







