import '@/api/rank'

export const Toplist = class {
	constructor(title, frontPicUrl, song, topId) {
	 this.title = title
	 this.frontPicUrl = frontPicUrl
	 this.song = song
	 this.topId = topId	
	}
	
}

export function createToplist(group) {
	let {title, frontPicUrl, song, topId} = group
	return new Toplist(title, frontPicUrl, song, topId)
}

export const Song = class {
	constructor(singer, songName, songMid, songId, albumName, albumMid, interval) {
		this.singerName = getSingerName(singer)
		this.singerMid = singer[0].mid
		this.songId = songId
		this.songName = songName
		this.songMid = songMid
		this.albumName = albumName
		this.albumMid = albumMid
		this.interval = interval
		this.songImg = getImgUrl(albumMid)
		// this.url = url
	}
}



export function createSong(song, cameback) {
	let {singer, name, mid, id, album, interval} = song
   return new Song(singer, name, mid, id, album.name, album.mid, interval)	
	
}



function getSingerName(singerName) {
	let nameStr = []
	singerName.map(item => {
		nameStr.push(item.name)
	})
	return nameStr.join('-')
}

function getImgUrl(albumMid) {

	return `https://y.gtimg.cn/music/photo_new/T002R300x300M000${albumMid}.jpg?max_age=2592000`
}



