export const recommendListSong = class {
	constructor(name, dissName, dissId, imgUrl) {
		this.name = name
		this.dissName = dissName
		this.dissId = dissId
		this.imgUrl = imgUrl
	}
}

export function createSongList(songList) {
	let {dissname, dissid, imgurl} = songList
	let name = songList.creator.name
	return new recommendListSong(name, dissname, dissid, imgurl)
}


