<template>
  <div id="app">
   <mHeader></mHeader>
   <div class="nav_wrapper">    
   <ul class="navs">
     <li class="nav-item"><router-link class="nav_text"  to="/recommend"></router-link></li>
     <li class="nav-item"><router-link class="nav_text"  to="/singer"></router-link></li>
     <li class="nav-item"><router-link class="nav_text"  to="/rank"></router-link></li>
     <li class="nav-item"><router-link class="nav_text"  to="/search"></router-link></li>
   </ul>
   </div>
   <keep-alive>
    <router-view></router-view>
  </keep-alive>
  <div class="music_play_wrapper">
    <transition>
   <MusicPlay v-show="get_current_index!==-1"></MusicPlay>
 </transition>
  </div> 
  </div>

</template>

<script>
import MusicPlay from '@/components/musicPlay/musicPlay'   
import mHeader from '@/components/header/header' 
import { mapGetters } from 'vuex'  
export default {
  components: {
    mHeader,
    MusicPlay
  },
  computed: {
    ...mapGetters([
      'get_current_index'
      ])
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/">
@import '~@/common/stylus/variable.styl'
#app
 .nav_wrapper
   position: relative;
   z-index: 1;
   background: $color-background 
  .navs
   display: flex
   padding-bottom: 5px
   .nav-item
    flex: 1
    text-align: center
    .nav_text
     display: inline-block
     padding-bottom: 4px
     font-size: $font-size-small
     font-weight: 700
     border-bottom: 1px solid transparent
    .router-link-exact-active,.router-link-active
     color: $color-theme
     border-bottom: 2px solid $color-theme;
     
     
     
        
            
       
</style>



