<template>
<div class="loading">
	<div class="loading_img_wrapper">
		<img src="./loading.gif" alt="" width="24" height="24">
		<p class="desc">
			{{message}}
		</p>
	</div>
</div>
</template>	
<script type="text/javascript">
	export default {
		props: {
			message: {
				type: String,
				default: '...'
			}
		}
	}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
  .loading
   .loading_img_wrapper
   	font-size: 12px	
    text-align: center
    .desc
     margin-top: 10px	
</style>


