import Vue from 'vue'
import Router from 'vue-router'
import recommend from '@/components/recommend/recommend'
import singer from '@/components/singer/singer'
import singerById from '@/components/singerById/singerById'
import rank from '@/components/rank/rank'
import search from '@/components/search/search'
import user from '@/components/user/user'

Vue.use(Router)

export default new Router({
 routes: [
 	{path: '/', redirect: '/recommend'},
 	{path: '/recommend', component: recommend, children: [
 		 {path: '/recommend/:id', component: singerById}
 	]},
 	{path: '/singer', component: singer,children: [
 	  {path: '/singer/:id', component: singerById}
 	]},
 	{path: '/rank', component: rank, children: [
 	 {path:'/rank/:topId', component: singerById}
 	]},
 	{path: '/search', component: search, children:[
 		{path: '/search/:id', component: singerById}
 	]},
 	{path: '/user', component: user}
 	

 ]
})



