<template>
<transition name="move">	
<div class="user">
	<div class="header">
		<div class="back" @click="_back">
			<i class="icon-back"></i>
		</div>
		<div class="choose">
			<div class="like" @click="_goLove" :class="{active: currentText === 'love'}">
				
			</div>
			<div class="recnetly" @click="_goRecnetly" :class="{active: currentText === 'recnetly'}">
				
			</div>
		</div>
		<div class="rand_wrapper" @click="_selectRandomPlay">
			<span class="icon">
				<i class="icon-play"></i>
			</span>
			<span class="text"></span>
		</div>
	</div>
	<Scroll class="content" :watcher="get_love_history" v-if="currentText === 'love'">
		<SongList 
		@palyMusic="_playMusic"
		:songList="get_love_history"
		v-show="currentText === 'love'"
		></SongList>
	</Scroll>
	<Scroll class="content" :watcher="get_recently_history" v-if="currentText === 'recnetly'">
		<SongList 
		@palyMusic="_playMusic"
		:songList="get_recently_history"
		v-show="currentText === 'recnetly'"
		></SongList>
	</Scroll>
</div>
</transition>
</template>	
<script type="text/javascript">
import SongList from '@/base/songList/songList'
import storage from 'good-storage'
import Scroll from '@/base/scroll/scroll'
import { mapGetters, mapMutations, mapActions} from 'vuex'	
import { random } from '@/common/js/random'
import { Base64 } from 'js-base64'	
import {getplaysongvkey, getLyrics} from '@/api/singer'
import { createSong } from '@/common/js/search.js'
import { ERR_OK } from '@/api/config'
import { setRecently, randomPlay } from '@/common/js/mixin'
	export default {
		mixins: [setRecently, randomPlay],
		components: {
			SongList,
			Scroll
		},
		data() {
			return {
				songList: [],
				recnetlySongList: [],
				currentText: 'love'
			}
		},
		computed: {
			...mapGetters([
				'get_play_list',
				'get_recently_history',
				'get_love_history'
				])
		},
		methods: {
			_back() {
				this.$router.back()
			},
			_selectRandomPlay() {
				let songList = this.currentText === 'love' ? this.get_love_history : this.get_recently_history 
				this._randomPlay(songList)
			},
			_playMusic(song) {
				let curIndex = 0
				this._getLyrics(song.songMid, (res) => {
					let formatStr = Base64.decode(res) 
					this.SET_LYRICS(formatStr)
				})
				if(this.currentText === 'love') {
					song.playList = this.get_love_history
					song.sequenceList = this.get_love_history.slice()
				}else {
					song.playList = this.get_recently_history
					song.sequenceList = this.get_recently_history.slice()

				}
				curIndex = song.playList.findIndex(item => song.songMid === item.songMid)
				this.singerMid = song.singerMid
				song.currentIndex = curIndex
				song.fullPlay = true
				song.playStatus = true
				song.lyrics = ''
				this.playMusicData = {...song}
				getplaysongvkey(song.songMid).then(res => {
					let url ='http://dl.stream.qqmusic.qq.com/' + res
					this.SET_URL(url)
					
				}) 
				song.url = ''
				this.setSongInfo(song)
				this._setRecently(song)
			},
			_getLyrics(singId, cameBack) {
				getLyrics(singId).then(res => {
					if(res.code === ERR_OK) {
						cameBack(res.lyric)
					}
				})
			},
			_goLove() {
				this.currentText = 'love'
			},
			_goRecnetly() {
				this.currentText = 'recnetly'
			},
			...mapMutations([
				'SET_LYRICS',
				'SET_LYRICS',
				'SET_URL'
				]),
			...mapActions([
				'setSongInfo'
				])
		}

	}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus" >
  @import '~@/common/stylus/variable.styl'
    .move-enter, .move-leave-to
     transform: translate3d(100%, 0, 0)
    .move-enter-active, .move-leave-active
     transition: all 0.3s	 
	.user
	 position fixed	
	 width 100%
	 height 100%
	 background $color-background 
	 z-index 1
	 left 0px
	 top: 0px
	 .header
	  width 100%
	  .back
	   padding: 5px	
	   position absolute	
	   top: 10px
	   color: $color-theme
	   font-size 25px
	   left 10px
	  .choose
	   width 50%
	   margin 10px auto
	   font-size 0px
	   border-radius 5px
	   overflow hidden
	   .like, .recnetly
	   	display inline-block
	   	box-sizing border-box
	   	padding 10px
	   	width 50%
	    text-align center
	    font-size $font-size-medium
	    // background $color-highlight-background
	    background $color-background-d 	
	   .active
	   	background $color-highlight-background
	   	// background $color-background-d 
	  .rand_wrapper
	   width 30%	 	
	   margin: 20px auto
	   padding: 10px 15px
	   border: 1px solid $color-highlight-background
	   border-radius: 20px
	   text-align center 
	   font-size $font-size-small
	   color: $color-text-l
	   .icon
	    vertical-align middle
	    margin-right 5px
	   	font-size $font-size-medium-x 
	 .content
	  overflow hidden	
	  height calc(100vh - 106px)
	  	   	
	    
	   		
</style>


