function getSingerName(singerName) {
	let nameStr = []
	singerName.map(item => {
		nameStr.push(item.name)
	})
	return nameStr.join('-')
}

function getImgUrl(albumMid) {

	return `https://y.gtimg.cn/music/photo_new/T002R300x300M000${albumMid}.jpg?max_age=2592000`
}



