import jsonp from '@/common/js/jsonp.js'
import axios from 'axios'

export function getHotSearch() {
	let url = '/getHotSearch'

	let data = {
		g_tk:5381,
		uin:0,
		format:'json',
		inCharset:'utf-8',
		outCharset:'utf-8',
		notice:0,
		platform:'h5',
		needNewCode:1
	}

	return axios.get(url, {
		params: data
	}).then( res => {
		return Promise.resolve(res.data.data.hotkey)
	}).catch( err => {
		return Promise.reject(err)
	})
}

export function getQueryResult(query, curNum) {
	let url = '/getQueryResult'
	let data = {
		g_tk:5381,
		uin:0,
		format:'json',
		inCharset:'utf-8',
		outCharset:'utf-8',
		notice:0,
		platform:'h5',
		needNewCode:1,
		w:query,
		zhidaqu:1,
		catZhida:1,
		t:0,
		flag:1,
		ie:'utf-8',
		sem:1,
		aggr:0,
		perpage:20,
		n:20,
		p:curNum,
		remoteplace:'txt.mqq.all',
	}

	return axios.get(url, {
		params: data
	}).then( res => {
		return Promise.resolve(res.data)
	}).catch( err => {
		return Promise.reject(err)
	})
}


