<template>
<transition name="show">
<div class="confirm" v-show="isShow">
	<div class="title">
		{{title}}
	</div>
	<div class="btns">
		<span class="btn_left" @click="_cancel">{{btnsText[0]}}</span><span class="btn_right" @click="_okey">{{btnsText[1]}}</span>
	</div>
</div>
</transition>
</template>	
<script type="text/javascript">
	export default {
		props: {
			title: {
				type: String,
				default: ''
			},

			btnsText: {
				type: Array,
				default: () => {
					return ['', '']
				}
			}
		},

		data() {
			return {
				isShow: false
			}
		},

		methods: {
			_cancel() {
				this.$emit('_cancel', false)
				this.hide()
			},
			_okey() {
				this.$emit('okey', true)
				this.hide()
			},
			hide() {
				this.isShow = false
			},
			show() {
				this.isShow = true
			}
		}
	}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
 @import '~@/common/stylus/variable.styl'
 .show-enter, .show-leave-to
  opacity 0			 
 .show-enter-active, .show-leave-active
  transition: all 0.2s	 
	.confirm
	 width: 250px
	 // padding: 10px
	 background: $color-highlight-background 
	 border-radius 20px
	 margin: 0 auto
	 color: $color-text-l
	 .title
	  padding: 20px 0	
	  text-align center
	  border-bottom 1px solid $color-background-d
	 .btns
	  text-align center
	  .btn_left, .btn_right	 	
	   display inline-block
	   box-sizing border-box
	   padding 15px 0
	   width 50%
	   text-align center
	   border-right 1px solid $color-background-d
	  .btn_right
	   border-right none	 
	 	
</style>


