<template>
	<div class="header">
	 <div class="header_wrapper">
	 	<div class="brand">
	 	</div>
	 	<span class="text">
	 		chicken music
	 	</span>
	 </div>
    <div class="mine" @click="_goUser">
       <i class="icon icon-mine"></i>
    </div>
	</div>
</template>
<script type="text/javascript">
	export default {
      methods: {
         _goUser() {
            this.$router.push('/user')
         }
      }
   }
</script>
<style scoped lang="stylus" res="stylesheet/stylus">
 @import '~@/common/stylus/mixin.styl'
 @import '~@/common/stylus/variable.styl'
 .header
   position: relative
   z-index: 1
   background: $color-background   
  .header_wrapper
   text-align: center;
   padding-top: 3px;
   height: 36px;
   line-height: 36px;
   .brand
    display: inline-block;
    margin-right: 3px;
    width: 22px;
    height: 23px;
    vertical-align: top;
    // background: url('./logo@2x.png') center center;
    bg-image('./logo')
    background-size: 22px 23px;
   .text
    display: inline-block;
    height: 23px;
    line-height: 23px;
    font-size: $font-size-small;
    color: $color-theme;
    vertical-align: top;
  .mine
   position absolute    
   right 10px
   padding 5px
   top: 5px
   .icon
    top: 0px 
    right 10px
    width 30px
    // height 30px
   .icon-mine
    font-size 20px
    color $color-theme
     
   				
</style>


