<template>
<div class="alert">
	<span><i class="icon-ok"></i></span><span v-text="message"></span>
</div>
</template>	
<script type="text/javascript">
	export default {
		props: {
			message: {
				type: String,
				default: ''
			}
		}
	}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
	@import '~@/common/stylus/variable.styl'
	.alert
	 height 40px
	 text-align center
	 background $color-highlight-background
	 line-height 40px
	 
</style>


