<template>
<Scroll class="query_list" :watcher="queryList" :pullUpLoad="true" @pullUpLoad="pullUpLoad">
	<ul class="query_list_wrapper">
		<li class="query" v-for ="(query, index) in queryList" @click="_formatSong(query)">
			<div class="icon_wrapper" v-show="!query.zhida">
				<i class="icon-music"></i>
			</div>
			<div class="name" v-show="!query.zhida">
				{{query.songname}} - {{getSingerName(query.singer)}}
			</div>
			<div class="icon_wrapper" v-show="query.zhida">
				<i class="icon-mine"></i>
			</div>
			<div class="name" v-show="query.zhida">
				{{query.singername}}
			</div>
			
		</li>	
	</ul>
	<router-view :songsListByMid="songsListByMid"></router-view>
</Scroll>	
</template>	
<script type="text/javascript">
import Scroll from '@/base/scroll/scroll'
import storage from 'good-storage'
import { Base64 } from 'js-base64'	
import {getplaysongvkey, getLyrics} from '@/api/singer'
import {getSingersList, getSingerById, getSingers}	from '@/api/singer'
import { createSong } from '@/common/js/search.js'
import {createSinger, createSong as newcreateSong} from '@/common/js/singer'
import { mapActions, mapGetters, mapMutations } from 'vuex'
import { setRecently } from '@/common/js/mixin'	
import { ERR_OK } from '@/api/config'
	export default {
		mixins: [setRecently],
		components: {
			Scroll
		},
		props: {
			queryList: {
				type: Array
			}
		},
		data() {
			return {
				songsListByMid: []
			}
		},
		computed: {
			...mapGetters([
				'get_play_list',
				'get_sequence_list',
				'get_full_play'
				])
		},
		methods: {
			getSingerName(singer) {
				let nameArr = []
				singer = singer || []
				singer.map(singer => nameArr.push(singer.name))
				return nameArr.join('/')
			},

			pullUpLoad() {
				this.$emit('pullUpLoad')
			},

			_formatSong(querySong) {
				if(querySong.zhida) {
					console.log(querySong)
					 this._getSingerById(querySong.singermid)
					 this.$emit('addHistory')
					 // this.$router.push(`/seach/${querySong.singermid}`)
					 return
				}
				this._playMusic(createSong(querySong)) 
				this.$emit('addHistory')
			},
			_getSingerById(singerByMid) {
		 		getSingerById(singerByMid).then(res => {
		 				console.log(res.data.list)
		 				this.songsListByMid.splice(0, this.songsListByMid.length)
		 				res.data.list.map(song => {
		 					this.songsListByMid.push(newcreateSong(song.musicData)) 
		 			})

		 			 this.$router.push(`/search/${singerByMid}`)
		 		})
		 	},
			_playMusic(song) {
				this._getLyrics(song.songMid, (res) => {
					let formatStr = Base64.decode(res) 
					this.SET_LYRICS(formatStr)
				})
				console.log(song)
				if(!this.get_play_list.length) {
					song.playList = new Array(song)
				}else {
					 let temArr = this.get_play_list.slice()
					 song.playList = temArr.slice()
					 song.playList.unshift(song)
				}
				song.sequenceList = song.playList.slice()
				this.singerMid = song.singerMid
				song.currentIndex = 0
				song.fullPlay = true
				song.playStatus = true
				song.lyrics = ''
				this.playMusicData = {...song}
				console.log(song)
				getplaysongvkey(song.songMid).then(res => {
					let url ='http://dl.stream.qqmusic.qq.com/' + res
					this.SET_URL(url)
					
				}) 
				song.url = ''
				this.setSongInfo(song)
				this._setRecently(song)
				

			},
			_getLyrics(singId, cameBack) {
				getLyrics(singId).then(res => {
					if(res.code === ERR_OK) {
						cameBack(res.lyric)
					}
				})
			}, 
			
			...mapMutations([
				'SET_URL',
				'SET_LYRICS'
				]),

			...mapActions([
					'setSongInfo' 
				])
		}
	}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
 @import '~@/common/stylus/variable.styl'	
 .query_list
  height: 100%
  overflow: hidden
  .query_list_wrapper
   .query
   	 display: flex
   	 padding-bottom: 20px
   	 color: $color-text-d
   	 font-size: $font-size-medium
   	 overflow: hidden
   	.icon_wrapper
   	 flex: 0 0 40px	
   	 text-align: center
   	 .inco-music	
   	  font-size: 28px
   	.name
   	 flex: 1
   	 white-space: nowrap	  			
   	 overflow: hidden
   	 text-overflow: ellipsis
</style>


