<template>
<transition name="move">	
<div class="singerById" ref="singerById">
	<div class="dialong" ref="dialong"></div>
	<div class="singer_by_id_wrapper" >
		<div class="header" ref="headerEl">
			<h2 class="title">{{songsListByMid[0].singerName}}</h2>
			<div class="back" @click="_back">
				<i class="icon-back"></i>
			</div>
			<div class="avater">
				<img class="avater_img"  v-lazy="songsListByMid[0].songImg" alt="">
			</div>
			<div class="playBtn">
				<div class="content_wrapper" @click="_randomPlay(songsListByMid)">
				<i class="icon-play"></i><span class="text"></span>
				</div>
			</div>
		</div>
		<Scroll class="content" :watcher="songsListByMid" :probeType="probeTypeNum" @scroll="_scrollToTop" ref="contentEl" >
			<songList :rank="rank" :songList="songsListByMid" @palyMusic="_playMusic" v-if="songsListByMid.length"></songList>
		</Scroll>
		
	</div>
	<!-- <transition name="music">	 -->
	<!-- 
	 -->
	<!-- </transition> -->	
</div>
</transition>
</template>	
<script type="text/javascript">	
import songList from '@/base/songList/songList' 
import Scroll  from '@/base/scroll/scroll'
import { Base64 } from 'js-base64'	
import musicPlay from '@/components/musicPlay/musicPlay' 
import {getplaysongvkey, getLyrics} from '@/api/singer'
import {random} from '@/common/js/random'
import storage from 'good-storage'
import { mapActions, mapGetters, mapMutations } from 'vuex'
import { ERR_OK } from '@/api/config'
import { setRecently, randomPlay } from '@/common/js/mixin'
	export default {
		mixins: [setRecently, randomPlay],
		components: {
			songList,
			Scroll,
			musicPlay
		},
		props: {
			songsListByMid: {
				type: Array,
				default: ()=> {
					return []
				}
			},
			rank: {
				type: Boolean,
				default: false
			}
		},
		data() {
			return {
				probeTypeNum: 3,
				playMusicData: null,
				show: true,
				singerMid: ''
			}
		},
		created() {
		},
		mounted() {
		 this._initScollCla()
		},
		watch: {
		},
		computed: {
			...mapGetters([
				'get_current_index',
				'get_play_model',
				'get_play_list',
				'get_song_mid'
				])
		},
		methods: {
			_back() {
				// this.show = false
				this.$router.back()
			},
			_scrollToTop(y) {
				if(y > 0) {
					let precent = (y*3+this.headerElrHei)/ this.headerElrHei
					this.$refs.headerEl.children[2].style.transform = `scale(${precent})`
					this.$refs.dialong.style.transform = `translate3D(0,${y}px,0)`
					this.$refs.contentEl.$el.style.transform = `translate3D(0,${y}px,0)`
					return
				}
				y = Math.abs(Math.floor(y))
				let dialongHeight = Math.min(y, this.headerElrHei-40)
				this.$refs.dialong.style.transform= `translate3D(0,${-dialongHeight}px, 0)` 
				let dirHeight = this.headerElrHei - dialongHeight
				 
				if(dirHeight<= 40) {
					this.$refs.headerEl.children[2].style.paddingTop = '40px'
					this.$refs.headerEl.children[3].style.display = 'none'
					this.$refs.headerEl.style.zIndex = 100

				}else {
					this.$refs.headerEl.children[2].style.paddingTop = '70%'
					this.$refs.headerEl.children[3].style.display = 'block'
					this.$refs.headerEl.style.zIndex = 1
				}
			},
			_initScollCla() {
				this.initedElFlag = false	
				let singerByIdHei = this.$refs.singerById.offsetHeight
				let headerElrHei = this.$refs.headerEl && this.$refs.headerEl.offsetHeight 
				this.headerElrHei = headerElrHei
				this.$refs.contentEl.$el.style.top = `${headerElrHei}px`
				this.contentElTop = headerElrHei
				this.$refs.dialong.style.top = `${headerElrHei + 1}px`
				this.$refs.dialong.style.height = `${headerElrHei - 40}px
				`
				this.$refs.contentEl.$el.style.height = `${singerByIdHei - headerElrHei}px
				`
				this.$refs.contentEl.refresh()
			},
			// _randomPlay() {
			// 	let temArr = random(this.songsListByMid.slice(0))
			// 	this.SET_PLAY_LIST(temArr)
			// 	this.SET_SEQUENCE_LIST(this.songsListByMid)
			// 	this.SET_PPLAY_MODEL(2)
			// 	temArr[0].currentIndex = 0
			// 	this._playMusic(temArr[0])

			// },
			_playMusic(song) {
				this._getLyrics(song.songMid, (res) => {
					let formatStr = Base64.decode(res) 
					this.SET_LYRICS(formatStr)
				})
				song.sequenceList = this.songsListByMid.slice(0)
				if(this.get_play_model === 0) {
					song.playList = this.songsListByMid.slice(0)
				}else if(this.get_play_model === 2) {
					if(this.get_play_list.length && song.singerMid === this.singerMid) {
						song.playList = this.get_play_list
					}else {
					
						song.playList = random(this.songsListByMid.slice(0))
						
					}
				}
				// song.playList = this.songsListByMid.slice(0)
				this.singerMid = song.singerMid
				song.fullPlay = true
				song.playStatus = true
				song.lyrics = ''
				this.playMusicData = {...song}
				console.log(song)
				getplaysongvkey(song.songMid).then(res => {
					let url ='http://dl.stream.qqmusic.qq.com/' + res
					this.SET_URL(url)
					
				}) 
				song.url = ''
				this.setSongInfo(song)
				this._setRecently(song)
			},
			_getLyrics(singId, cameBack) {
				getLyrics(singId).then(res => {
					if(res.code === ERR_OK) {
						cameBack(res.lyric)
					}
				})
			}, 
			...mapMutations([
				'SET_URL',
				'SET_LYRICS',
				'SET_PLAY_LIST',
				'SET_SEQUENCE_LIST',
				'SET_PPLAY_MODEL'
				]),
			...mapActions([
				'setSongInfo'
				])
		}
	} 
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
   @import '~@/common/stylus/variable.styl'
    .move-enter, .move-leave-to
     transform: translate3d(100%, 0, 0)
    .move-enter-active, .move-leave-active
     transition: all 0.3s linear	 		
	.singerById
	 position: absolute
	 left: 0
	 top: 0
	 z-index: 10
	 background: $color-background
	 height: 100%
	 width: 100%
	 overflow: hidden	 
	 .music_play_wrapper
	  // position: absolute
	  // left: 0px
	  // top: 0px
	  // width: 100%
	  // height: 100%
	  // z-index: 100	
	 .dialong 
	  position: absolute
	  z-index: 10	
	  height: 20px	
	  width: 100%
	  background: $color-background
	 .singer_by_id_wrapper
	  .header
	   position: relative
	   .title
	   	position: absolute
	   	z-index: 1
	   	top: 0
	   	left: 0
	   	width: 100%
	   	text-align: center
	   	height: 40px
	   	line-height: 30px
	   	font-size: $font-size-medium-x
	   .playBtn
	   	position:absolute
	   	width: 100%	
	   	text-align: center
	   	bottom: 35px
	   	.content_wrapper
	   	 display: inline-block	
	   	 width: 136px
	   	 height: 30px	
	   	 line-height: 30px
	   	 border-radius: 15px
	   	 border: 1px solid $color-theme
	   	 color: $color-theme
	   	 font-size: $font-size-medium	
	   	 .text
	   	  margin-left: 4px		
	   .back
	   	position: absolute
	   	left: 0px
	   	top: 6px
	   	z-index: 50
	   	display: block
	   	padding: 10px
	   	font-size: $font-size-large-x
	   	color: $color-theme		 
	   .avater
	    position: relative	
	    padding-top: 70%
	    overflow: hidden
	    background: $color-background
	    .avater_img
	     opacity: 0.8	
	   	 position: absolute
	   	 left: 0
	   	 top: 0
	   	 width: 100%
	   	 height: auto
	  .content
	   position: absolute
	   // padding-top: 70%
	   // padding-top: 100px
	   left: 0px
	   top: 0px
	   z-index :10
	   width: 100%
	   
	
</style>


