<template>
<div class="myAudio">
	<audio
	 :src="get_url"
	 @canplay="_canPlay"
	 @ended="_ended"
	 ref="myAudio"></audio> 
	<div class="myAudio_wrapper">
		<div class="progress_wrapper" 
		@click="_changeToNextTime($event)"
		
		>
			<div class="left_time">{{currentTime}}</div>
			<div class="progress" ref="progress" >
				<div class="progress_btn"
				 :style="{left: proBarLength}"
				 @touchstart.prevent="_touchstart"
				 @touchmove.prevent="_touchmove"
				 @touchend="_touchend"
				></div>
				<div class="paogress_bar" 
				:style="{width: proBarLength}"
				></div>
			</div>
			<div class="right_time">{{totalTime}}</div>
		</div>
		<div class="btns_wrapper">
			<div class="btn_item" @click.stop="_togglePlayMode">
				<i class="icon" :class="playModeClass"></i>
			</div>
			<div class="btn_item" @click="_prev">
				<i class="icon icon-prev"></i>
			</div>
			<div class="btn_item">
				<i class="icon" :class="playAndPauseClass" @click="_togglePlay"></i>
			</div>
			<div class="btn_item"  @click="_next">
				<i class="icon icon-next"></i>
			</div>
			<div class="btn_item">
				<i 
				 class="icon"
				 :class="_getFavoriteClass(get_play_list[get_current_index])"
				 ref="favorite" 
				 @click="_toggleFavorite(get_play_list[get_current_index])"></i>
			</div>
		</div>
	</div>
</div>
</template>	
<script type="text/javascript">
	import {mapGetters, mapMutations, mapActions} from 'vuex'
	import {getplaysongvkey, getLyrics} from '@/api/singer'
	import { getStorage, setStorage } from '@/common/js/localStorage'
	import storage from 'good-storage'
	import {random} from '@/common/js/random'
	import { ERR_OK } from '@/api/config'
	import { setLocalLove, togglePlayMode } from '@/common/js/mixin'
	export default {
		mixins: [setLocalLove, togglePlayMode],

		data() {
			return {
				currentTime: '0:00',
				proBarLength: '0px'
			}
		},
		created() {
			this.touch = {
				inited: false
			}
		},
		watch: {
			get_play_status(newVal) {
				newVal? this._play() : this._pause() 
			}
		},
		computed: {
			totalTime() {
				let m = Math.floor(this.get_interval / 60)
				let s = this.get_interval % 60
				s = s.toString().length === 1 ? 0 + s : s
				s = s < 10  ? `0${s}` : s
				return `${m}:${s}`
			},
			playAndPauseClass() { 
				return !this.get_play_status ? 'icon-play' : 'icon-pause'	
			},
			playModeClass() {
				return this.get_play_model === 0 ? 'icon-sequence' : this.get_play_model === 1 ? 'icon-loop' : 'icon-random' 
			},
			...mapGetters([
					'get_url',
					'get_interval',
					'get_play_status',
					'get_current_index',
					'get_play_model',
					'get_play_list',
					'get_sequence_list',
					'get_full_play'
				])
		},
		methods: {
			_canPlay() {
				if(this.get_play_status) {
				  this._play()
				  this._getCurrentTime()
				}
				
			},
			_ended() {
				this._next(false)
			},
			_togglePlay() {

				this.get_play_status ? this._pause() : this._play()

			},
			_play(notIsended) {
 				this.$refs.myAudio.play()
 				this.SET_PLAY_STATUS(true)	
 				
			},
			_pause() {
				this.$refs.myAudio.pause()
				this.SET_PLAY_STATUS(false)
				this.$emit('audioTime',  this.$refs.myAudio.currentTime)
			},
			_prev() {
				let curIndex = this.get_current_index
				let playList = this.get_play_list
				let lastIndex = this.get_play_list.length 
				curIndex = curIndex === 0 ? lastIndex : curIndex
				curIndex--
				this._palyMusic(playList[curIndex], curIndex) 

			},
			_next(notIsended = true) {
				
				let curIndex = this.get_current_index
				let playList = this.get_play_list
				let lastIndex = this.get_play_list.length
				curIndex = curIndex === lastIndex -1 ? -1 : curIndex 
				curIndex++
				this._palyMusic(playList[curIndex], curIndex, notIsended) 
			},
			_changeToNextTime($event){
				let {x} = $event
				let progressWidth = this.$refs.progress.offsetWidth
				let padLeft = (window.innerWidth - progressWidth)/2
				let dirX = x - padLeft
				let changeToTime = Math.floor((dirX / (progressWidth))*this.get_interval)
				this.$refs.myAudio.currentTime = changeToTime
				this.SET_CURRENT_TIME(changeToTime)
			},
			_touchstart($event) {
				this.touch.inited = true
				let first = $event.changedTouches[0]
				let startX = first.clientX
				console.log(startX) 
				this.touch.startX= startX
				this.touch.startTime = this.$refs.myAudio.currentTime

			},
			_touchmove($event) {
				if(!this.touch.inited ) return
				 let  first = $event.changedTouches[0]
				 let progressWidth = this.$refs.progress.offsetWidth
				 let moveX = first.clientX 
				 let dirX = moveX - this.touch.startX
				 let dirTime = Math.floor((dirX / (progressWidth))*this.get_interval)
				 let time = Math.min(this.get_interval, dirTime + this.touch.startTime)
				 this.$refs.myAudio.currentTime = time
				 this.SET_CURRENT_TIME(time)

			},
			_touchend($event) {
				this.touch.inited = false
			},
			_palyMusic(song, curIndex, notIsended = true) {
				if(this.get_play_model === 1 && !notIsended) {
					this.SET_LYRICS('')
					console.log('notIsended', notIsended)	
					curIndex = curIndex -1 >=0 ? curIndex -1 : curIndex
					this._getLyrics(this.get_play_list[curIndex].songMid, (res) => {

					let formatStr = Base64.decode(res)
					this.SET_LYRICS(formatStr)
				})	
					return this._play(notIsended)
				}
				this._getLyrics(song.songMid, (res) => {

					let formatStr = Base64.decode(res)
					this.SET_LYRICS(formatStr)
				})

				if(!song.fullPlay && notIsended) {
				   song.fullPlay = true
				}
				if(!notIsended) {
					song.fullPlay = this.get_full_play
				}
				song.playStatus = true
				song.lyrics = ''
				song.currentIndex = curIndex
				this.playMusicData = {...song}
				console.log(song.songMid)
				getplaysongvkey(song.songMid).then(res => {
					let url ='http://dl.stream.qqmusic.qq.com/' + res
					this.SET_URL(url)
				}) 
				song.url = ''
				this.changeList(song)
			},
			_getCurrentTime() {
				this.timer = setInterval(() => {
					if(!this.$refs.myAudio.currentTime) return
					let audio = this.$refs.myAudio
					this.currentTime = this._formatTime(audio.currentTime)
					this._computeProBarLength(audio.currentTime)

				}, 1000)
			},
			_formatTime(time) {
				let m = Math.floor(time / 60)
				let s = Math.floor(time % 60)
				s = s < 10  ? `0${s}` : s
				return `${m}:${s}`
			},
			_computeProBarLength(curTime) {
				this.$nextTick(()=>{
					let offWidth = this.$refs.progress.offsetWidth - 16
					let procent = curTime / this.get_interval
					this.proBarLength = (offWidth * procent) + 'px'
				})
			},
			_getLyrics(singId, cameBack) {
				getLyrics(singId).then(res => {
					if(res.code === ERR_OK) {
						cameBack(res.lyric)
					}
				})
			}, 
			...mapMutations([
				'SET_PLAY_STATUS',
				'SET_PPLAY_MODEL',
				'SET_PLAY_LIST',
				'SET_URL',
				'SET_LYRICS',
				'SET_CURRENT_TIME',
				'SET_LOVE_HISTORY'
				]),
			...mapActions([
				'setSongInfo',
				'changeList'
			])		
		}
	}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
    @import '~@/common/stylus/variable.styl'
	.myAudio_wrapper
	 width: 100%
	 .progress_wrapper
	  height: 30px	
	  display: flex	
	  .left_time,.right_time
	   flex: 0 0 50px
	   line-height: 30px	
	   text-align: center
	   // vertical-align:middle
	  .progress 
	   position: relative
	   flex: 1
	   height: 4px
	   box-sizing: border-box
	   margin-top: 13px
	   background: rgba(0, 0, 0, .3)
	   .progress_btn
	   	position: absolute
	   	top: -8px
	   	width: 14px
	   	height:14px
	   	border: 2px solid $color-text
	   	border-radius: 50%
	   	background: $color-theme
	   .paogress_bar
	   	position: absolute 
	   	z-index: 1	
	   	height: 100%
	   	background: $color-theme
	 .btns_wrapper
	  display: flex	
	  align-items: center  
	  .btn_item
	   flex: 1
	   text-align: center
	   .icon
	   	font-size: 30px
	   	color: $color-theme
	   .icon-play,.icon-pause
	   	font-size: 40px		
	 
</style>


