<template>
 <Scroll class="recommend" :watcher="recomendList" ref="scroll">
 	<div class="scroll_wrapper" ref="scroll_wrapper">
 	<div class="Banner_wrapper">
 	<Banner @picLoaded="_picLoaded" :slider="slider" v-if="slider.length"></Banner>
 	</div>
 	<ul class="song_list" >
 		<div>
	 	<h2 class="song_list_title">
			
	    </h2>
	 	<div class="song_list_content" v-show="recomendList.length">
	 		<ul class="songs">
	 			<li class="song" v-for="song in recomendList" @click="_getRecomList(song.dissId)">
	 				<div class="avatar">
	 					<img width="60" height="60" v-lazy="song.imgUrl" alt="">
	 				</div>
	 				<div class="song_content">
	 					<div class="name">
	 						{{song.name}}
	 					</div>
	 					<p class="text">
	 						{{song.dissName}}
	 					</p>
	 				</div>
	 			</li>
	 		</ul>
	 	</div>
	 </div>
 	</ul>
 	<div class="loading_wrapper" v-show="!recomendList.length">
 		<Loading></Loading>
 	</div>	
 	</div>
 	<transition name="move">
 	  <router-view :songsListByMid = "songsListByMid"></router-view>
 	</transition>
 </Scroll>	
</template>
<script type="text/javascript">
 import Banner from '@/base/banner/banner'
 import Scroll from '@/base/scroll/scroll'
 import Loading from '@/base/loading/loading'
 import MusicPlay from '@/components/musicPlay/musicPlay'

 import {getSeller, getSongList, getDissSong, getRecomList} from '@/api/recommend.js'
 import {createSong} from '@/common/js/singer'
 import {createSongList} from '@/common/js/recommend'
 import {ERR_OK} from '@/api/config.js'
 import { toggleBottom } from '@/common/js/mixin'
 import { mapGetters, mapMutations, mapActions} from 'vuex'	
 export default {
  
   components: {
   	Banner,
   	Scroll,
   	Loading,
   	MusicPlay
   },
    mixins:[toggleBottom],	
   data() {
   	return {
   		slider: [],
   		recomendList: [],
   		songsListByMid: []
   	}
   },	
   created() {
		this._getSeller(),
		this._getSongList(),
		this._getDissSong()
	},
	computed: {
		...mapGetters([
			'get_current_index'
			])
	},
	methods: {
	_picLoaded(val) {
		this.$nextTick(() => {
			this.$refs.scroll.refresh()
		})
	},	
	 _getSeller() {
	 	getSeller().then(res => {
	 		if(res.code === ERR_OK) {
	 			this.slider = res.data.slider
	 		}
	 	})
	 },
	 _getSongList() {
	 	getSongList().then(res => {
	 		console.log(res.data.category.data.category[0])
	 	})
	 },
	 _getDissSong() {
	 	getDissSong().then(res => {
	 		if(res.data.code === ERR_OK) {
	 			let temList = []
	 			res.data.data.list.map(song => {
	 				temList.push(createSongList(song))
	 			})
	 			
	 			this.recomendList = temList.slice(0)
	 		
	 			
	 		}
	 	})
	 },
	 _getRecomList(dissId) {
	 	console.log(dissId)
	 	getRecomList(dissId).then(res => {
	 		this.songsListByMid = []
	 		res.map(item => {
	 			this.songsListByMid.push(createSong(item))
	 		})
	 		console.log(this.songsListByMid)
	 		this.$router.push(`./recommend/${dissId}`)
	 	})
	 },
	 toggleBottom() {
	 	if (this.get_current_index === -1) {
	 		this.$refs.scroll_wrapper.style.paddingBottom = '0px'
	 	}else {
	 		this.$refs.scroll_wrapper.style.paddingBottom = '60px'
	 	}
	 }
	}	
 }

</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~@/common/stylus/variable.styl'
.move-enter, .move-leave-to
  // opacity: 0.5
.move-enter-active, .move-leave-active
  transition: all 0.3s
.recommend
  width: 100%
  height: calc(100vh - 65px)
 .scroll_wrapper
  position: absolute
  left: 0px
  top: 0px
  margin-top: 65px
  box-sizing: border-box
  width: 100%
  .Banner_wrapper	
   width: 100%
  .song_list
   .song_list_title
    text-align: center
   	height: 40px
   	line-height: 40px
   	color: $color-theme  
   	font-size: $font-size-medium
   .song_list_content
   	padding: 0 15px
   	.songs
   	 .song
   	  display: flex
   	  padding-top: 15px 
   	  .avatar
   	   flex: 0 0 60px
   	   margin-right: 15px			
 	  .song_content 	
 	   flex: 1
 	   font-size: $font-size-small
 	   .name
 	    color: $color-text
 	    height: 30px
 	    line-height: 30px
 	   .text
 	   	height: 25px
 	    line-height: 25px
 	   	color: $color-text-d  
  .loading_wrapper
   position: absolute
   width: 100%	
   top: 300px
</style>


