import storage from 'good-storage'
import { mapGetters, mapMutations, mapActions} from 'vuex'	
import { random } from '@/common/js/random'

/** 
 [queryList.vue],
 [singerById.vue:],
 [songsView.vue:],
 [user.vue:]
**/

export const setRecently = {
	methods: {
		_setRecently(song) {
			console.log(100000)
				if(!storage.has('recently')) {
					storage.set('recently', [])
				}
				let temArr = storage.get('recently')
				let index = temArr.findIndex(item => song.songMid === item.songMid)
				
				if(index >= 0) {
					temArr.splice(index, 1)	
				} 
				temArr.unshift({
					    singerName: song.singerName,
						albumMid: song.albumMid,
						interval: song.interval,
						albumName: song.albumName,
						singerMid: song.singerMid,
						songId: song.songId,
						songImg: song.songImg,
						songMid: song.songMid,
						songName: song.songName
					})
				storage.set('recently', temArr)
				this.SET_RECENTLY_HISTORY(temArr)
			},
			...mapMutations([
				'SET_RECENTLY_HISTORY'
				])
	}
}
/**
 [myAudio.vue]
 [songsView.vue]
 */
export const setLocalLove = {
	methods: {
				_toggleFavorite(song, elIndex) {
				let {songMid} = song
				if(!storage.has('favorite')) {
					storage.set('favorite', [])
				}
				let temArr = storage.get('favorite')
				let originBool = false
				let itemByMid = temArr.find(item => song.songMid === item.songMid)
				if(itemByMid) {
					originBool = itemByMid.favorite
				}
				let index = temArr.findIndex(item => song.songMid === item.songMid)
				if(index >= 0) {
					temArr.splice(index, 1)
				}

				temArr.unshift({
						singerName: song.singerName,
						favorite: !originBool,
						albumMid: song.albumMid,
						interval: song.interval,
						albumName: song.albumName,
						singerMid: song.singerMid,
						songId: song.songId,
						songImg: song.songImg,
						songMid: song.songMid,
						songName: song.songName

				})

				if( typeof elIndex !== 'undefined') {
					this.$refs.favorite[elIndex].className 
					 = this.$refs.favorite[elIndex].className === 'icon-favorite'
					  ? 'icon-not-favorite': 'icon-favorite'
				}
				storage.set('favorite', temArr)
				this.SET_LOVE_HISTORY(temArr)
				this.$refs.favorite.className  = this.$refs.favorite.className === 'icon icon-favorite' ? 'icon icon-not-favorite': 'icon icon-favorite'	

			},
			_getFavorite(song) {
				let temArr = storage.get('favorite')
				if(!temArr || !song) {
					return false
				}
				let itemByMid = temArr.find( item => item.songMid === song.songMid)
				if(!itemByMid) {
					return false
				}else {
					return itemByMid['favorite']
				}
			},
			_getFavoriteClass(song) {
				return this._getFavorite(song) ? 'icon-favorite' : 'icon-not-favorite'
			},
	}
}

/**
 [myAudio.vue]
 [songsView.vue]
 */
export const togglePlayMode = {
	computed: {
		...mapGetters([
			'get_play_model',
			'get_sequence_list',
			'get_current_index',
			'get_play_list'
			])
	},

	methods: {
		_togglePlayMode() {
				let nowPlayModel = this.get_play_model 
				nowPlayModel++
				nowPlayModel = nowPlayModel > 2 ? 0 : nowPlayModel
				this.SET_PPLAY_MODEL(nowPlayModel)
				if(this.get_play_model  === 0) {
					let curSong = this.get_play_list[this.get_current_index]
					let index = this.get_sequence_list.findIndex(item => curSong.songMid === item.songMid)
					console.log('index', index)
					this.SET_CURRENT_INDEX(index)
					return this.SET_PLAY_LIST(this.get_sequence_list) 
				}
				if(nowPlayModel  === 2) {
					if(!this.get_sequence_list) return
					let temArr = this.get_sequence_list.slice()
					let randomPlayList = random(temArr)
					let curSong = this.get_sequence_list[this.get_current_index]
					console.log(curSong)
					let index = randomPlayList.findIndex(item => curSong.songMid === item.songMid)
					console.log('index', index)
					this.SET_CURRENT_INDEX(index)
					return this.SET_PLAY_LIST(randomPlayList) 
				}
			},

			...mapMutations([
				'SET_PLAY_LIST',
				'SET_PPLAY_MODEL',
				'SET_CURRENT_INDEX'
				])
	}
}

export const randomPlay = {
	methods: {
		_randomPlay(songList) {
				let temArr = random(songList.slice(0))
				this.SET_PLAY_LIST(temArr)
				this.SET_SEQUENCE_LIST(songList)
				this.SET_PPLAY_MODEL(2)
				temArr[0].currentIndex = 0
				this._playMusic(temArr[0])
			},
			 ...mapMutations([
			 	'SET_PLAY_LIST',
			 	'SET_SEQUENCE_LIST',
			 	'SET_PPLAY_MODEL'
			 	])
	}
} 

export const toggleBottom = {
	watch: {
		get_current_index: {
			handler() {
				this.toggleBottom()
			}
			
		}
	},
	activated() {
		console.log(88888888)
		this.toggleBottom()
	},
	computed: {
		...mapGetters([
			'get_current_index'
			])
	}
}


