<template>
<transition 
name="music"
@enter="enter"
@after-enter="afterEnter"
@leave="leave"
@after-leave="leaveAfter"
>	
<div class="music_play_wrapper" v-show="get_current_index !== -1">
  <transition 
  name="music"
  @enter="enter"
  @after-enter="afterEnter"
  @leave="leave"
  @after-leave="leaveAfter"
  >
	<div class="normal_play" v-show="get_full_play" :style="{height: normal_play_height()}">
    <div class="background">
    <img class="bg_img" :src="get_song_img" alt="">
    </div>
    <div class="header">
       <div class="back"  @click="_back">
        <i class="icon-back"></i>
       </div>
       <div class="song_name " >
        {{get_song_name}}
       </div>
       <div class="singer_name">
        {{get_singer_name}}
       </div>
      </div>
    <div 
    class="song_conten_lyrics_wrapper"
    ref="song_lyrics_wrapper"
    :style="{height: _height()}"
    @touchstart.prevent="_touchstart($event)"
    @touchmove.prevent="_touchmove($event)"
    @touchend="_touchend($event)"
    >
     <div class="song_wrapper" ref="song_wrapper">
			<div class="avatar_wrapper"
      ref="avatar_wrapper" 
      v-show="get_current_index != -1"
      >
				<div class="second_avatar_wrapper avatar_move" :class="awatarPause"  ref="second_avatar_wrapper">
					<img class="avatar_mig" style="width: 100%;" :src="get_song_img" alt="">
				</div>
			</div>
     </div> 
      <div class="lyrics_wrapper" ref="lyrics_wrapper">
          <Lyrics ref="lyrics" v-if="get_lyrics !==''"></Lyrics>
      </div>
      </div>
      <div class="dots">
        <span class="dot" :class="{active: status === 'cd'}"></span><span class="dot" :class="{active: status === 'lyrics'}"></span>
      </div>
			<div class="audio_wrapper">
				<myAudio ref="myAudio"></myAudio>
			</div>
		
	</div>
</transition>

	<div class="mini_play" v-show="!get_full_play">
			<div class="avater" @click="_showNormal">
				<img :src="get_song_img" class="avatar_move" :class="awatarPause" height="40" width="40" alt="">
			</div>
			<div class="info">
				<div class="song_name">
					{{get_song_name}}
				</div>
				<div class="singer_name">
					{{get_singer_name}}
				</div>
			</div>
		<div class="min_btns">
		    <i class="icon-mini" @click="_togglePlay" :class="playAndPauseClass" ></i>
			<i class="icon-playlist" @click="_toggleMuneShow"></i>
		</div>
	</div>
  <!-- <div class="songs_view_wrapper"> -->
   <SongsView ref="songsView"></SongsView>
 <!--  </div>  -->
</div>
</transition>
</template>	
<script type="text/javascript">
 import Scroll from '@/base/scroll/scroll'
 import SongsView from '@/components/songsView/songsView'
 import {random} from '@/common/js/random'
 import {mapGetters, mapActions, mapMutations} from 'vuex'
 import myAudio from '@/base/myAudio/myAudio'
 import Lyrics from '@/components/lyrics/lyrics'
 import animations from 'create-keyframe-animation'	

	export default {
		components: {
			myAudio,
      Lyrics,
      Scroll,
      SongsView
		},
		data() {
			return {
	     backClick: false,
       musicTime: 0,
       snap: false,
       status: 'cd'
			}
		},
    created() {
      this.touch = {
        inited: false
      }
    },
		methods: {
      _togglePlay() {
        let myAudio = this.$refs.myAudio
        console.log(myAudio)
        this.get_play_status ? myAudio._pause() : myAudio._play()
      },
      _touchstart($event) {

        this.touch.inited = true
        let first = $event.touches[0]
        let startX = first.clientX
        let startY = first.clientY
        this.touch.startX = startX
        this.touch.startY = startY
      },
      _touchmove($event) {
        let {min, max, floor, abs} = Math
        let first = $event.touches[0]
        let startX = first.clientX
        let startY = first.clientY
        let dirY = startY -this.touch.startY 

        let dirX = this.status === 'cd' ? min(max(floor(startX - this.touch.startX), -window.innerWidth), 0) : min(max(floor(startX - this.touch.startX), 0),window.innerWidth)
        this.dirY = abs(dirY)
        this.dirX = abs(dirX)
        if(abs(dirY)>abs(dirX)){
          return
        } 
        let precent = abs(dirX) / window.innerWidth
        this.status === 'cd' ? this.$refs.lyrics_wrapper.style.transform = `translate3d(${dirX}px, 0, 0)` : this.$refs.lyrics_wrapper.style.transform = `translate3d(${-window.innerWidth + dirX}px, 0, 0)`
        this.status === 'cd' ? this.$refs.song_wrapper.style.opacity = 1 - precent :
        this.$refs.song_wrapper.style.opacity = precent
        this.touch.dirX = dirX 
        this.$refs.song_wrapper.style.transitionDuration = '0s'
        // this.$refs.lyrics.$el.style.transitionDuration = '0s'
        this.$refs.lyrics_wrapper.style.transitionDuration = '0s'
      },
      _touchend($event) {
        if(this.dirY > this.dirX){
          this.touch.inited = false
          return
        } 
        let precent = Math.abs(this.touch.dirX) / window.innerWidth
        let song_wrapper = this.$refs.song_wrapper
        // let lyrics = this.$refs.lyrics.$el
        let lyrics = this.$refs.lyrics_wrapper
        if(precent < 0.1 && this.touch.dirX < 0) {
          // song_wrapper.style.transition = 'all 0.3s'
          song_wrapper.style.opacity = ''
          song_wrapper.style.transform = 'translate3d(0, 0, 0)'
          return
        }
        if(precent < 0.1 && this.touch.dirX > 0) {
          lyrics.style.transitionDuration = '300ms'
          lyrics.style.transform = 'translate3d(-100%, 0, 0)'
          song_wrapper.style.opacity = 0
          return
        }
        if( precent > 0.1 && this.touch.dirX < 0) {
          lyrics.style.transitionDuration = '300ms'
           lyrics.style.transform = 'translate3d(-100%, 0, 0)'
           song_wrapper.style.opacity = 0
           this.status = 'lyrics'
          
        }else if(precent > 0.1 && this.touch.dirX > 0) {
           lyrics.style.transitionDuration = '300ms'
           lyrics.style.transform = 'translate3d(0, 0, 0)'
           song_wrapper.style.opacity = 1
           this.status = 'cd'
        }
        this.touch.inited = false
      },
      enter(el,  done) {
        const {x, y, scale} = this._getPosAndScale()
        let animation = {
          0: {
              transform: `translate3d(${-x}px,${y}px,0) scale(${scale})`
            },
            60: {
               transform: `translate3d(0,0,0) scale(1.2)`
            },
            100: {
              transform: `translate3d(0,0,0) scale(1)`
            }
        }
        animations.registerAnimation({
          name: 'music',
          animation,
          presets: {
            duration: 300,
            easing: 'linear'
          }
        }
          )
          animations.runAnimation(this.$refs.avatar_wrapper, 'music', done)
      },
      afterEnter(el) {
        console.log('end')
        this.backClick = true
        animations.unregisterAnimation('music')
        this.$refs.avatar_wrapper.style.animations = ''
      },
      leave(el, done) {
        
        this.$refs.avatar_wrapper.style.transition = 'all 0.3s'
        // this.$refs.avatar_wrapper.style.transition = 'all 0.4s'
        const {x, y, scale} = this._getPosAndScale()
        this.$refs.avatar_wrapper.style.transform =`translate3d(${-x}px,${y}px,0) scale(${scale})`
  
        this.$refs.avatar_wrapper.addEventListener('transitionend', done)
     
      },
      leaveAfter() {
        this.backClick = false
        this.$refs.avatar_wrapper.style.transtion = ''
        this.$refs.avatar_wrapper.style.transform =''
      },
      _getPosAndScale() {
        let bigWidth = window.innerWidth * 0.8
        let smallWidth = 40
        // let header = 60
        let fixedTop = 70
        let bottomCenter = 30
        let x = (window.innerWidth*0.1-smallWidth) + bigWidth/2 
        let y = window.innerHeight - fixedTop - bigWidth/2 - bottomCenter -10
        let scale = smallWidth / bigWidth
        return {
          x,
          y,
          scale
        }

      },
			_back() {
        this.SET_FULL_PLAY(false)
			},
      _showNormal() {
        this.SET_FULL_PLAY(true)
      },
      normal_play_height() {
        return window.innerHeight + 'px'
      },
      _audioTime(val) {
        this.musicTime = val
        this.$refs.second_avatar_wrapper.style.transition = ''
         let deg = 360/10 * (Math.floor(val))
        this.$refs.second_avatar_wrapper.style.transform = `rotate(${deg}deg)`
      },
      _startAudioTime() {
        this._avatarRotate()
      },
      _height() {
        return `${window.innerHeight - 80 -130}px `
      },
      _toggleMuneShow() {
        this.$refs.songsView.toggleShow()
      },
			...mapMutations([
				'SET_CURRENT_INDEX',
        'SET_FULL_PLAY',
        'SET_PPLAY_MODEL',
        'SET_PLAY_LIST '
				])
		},
		computed: {
      awatarPause() {
        return this.get_play_status ? '' : 'avatar_pause'
      },
      scrollWatchData() {
        return new Array(this.get_song_img, this.get_lyrics)
      },
      playAndPauseClass() { 
        return !this.get_play_status ? 'icon-play-mini' : 'icon-pause-mini' 
      },
			...mapGetters([
				'get_singer_name',
				'get_song_name',
				'get_song_img',
				'get_url',
				'get_current_index',
        'get_full_play',
        'get_url',
        'get_play_status',
        'get_lyrics',
        'get_play_model'
				])
		}
	}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
 @import '~@/common/stylus/variable.styl'
 .music-enter, .music-leave-to
  .header
   opacity: 0.5	
   transform: translate3d(0, -100%, 0)
  .audio_wrapper
   opacity: 0.5		
   transform: translate3d(0, 100%, 0)	 		
 .music-enter-active, .music-leave-active
  transition: all 0.3s
  .header
   transition: all 0.3s	 
  .audio_wrapper
    transition: all 0.3s	 		 
 .music_play_wrapper
  position: fixed
  top: 0px	
  left: 0px
  width: 100%
  z-index: 100
  .back
   z-index:1000	
   position: absolute	
   font-size: 22px
   color: $color-theme
   padding: 9px
   transform: rotate(-90deg)	 	
  .normal_play
   position: relative 
   background: $color-background  
   width: 100%
   .background
    position: absolute 
    width: 100%
    height: 100%
    filter: blur(20px)
    opacity: 0.6
    .bg_img
     height: 100%
     width: 100%
   .header
    position: relative 
    width: 100%
    text-align: center
    height: 60px
    .singer_name
     height: 20px
     line-height: 20px
     color: #fff
     font-size: $font-size-medium  
    .song_name
     height: 40px 
     line-height: 40px
     font-size: $font-size-large
   .song_conten_lyrics_wrapper 
    width: 200%
    font-size: 0px   
    .song_wrapper
     position: relative
     display: inline-block
     vertical-align: top
     height: 100%  
     width: 50% 
     overflow: hidden 
     .avatar_wrapper
   	  position: fixed
   	  top: 80px
   	  left: 10%
   	  margin: -10px 0px 0px -10px
   	  width: 80%
      
   	  border-radius: 50%
   	  border: 10px solid rgba(0, 0, 0, 0.1)
    	overflow: hidden
     .second_avatar_wrapper
      border-radius: 50%
      overflow: hidden
     .avatar_move  
      animation: move 20s linear infinite
     .avatar_pause
      animation-play-state: paused   
   .lyrics_wrapper
    display: inline-block
    vertical-align: top
    width: 50% 
   .dots
    position: fixed  
    bottom: 150px
    left: 50%
    transform translateX(-50%)
   .dot   
    display: inline-block
    margin-right: 8px
    width: 8px
    height: 8px
    background: $color-text-l
    border-radius: 50%
   .active
    width: 20px 
    background: $color-text-ll
    border-radius: 25%       
   .audio_wrapper
   	position: fixed
   	width: 90%
   	left: 5%
   	bottom: 50px
   		
	@keyframes move{
	 0%{transform: rotate(0) } 
	 100%{transform: rotate(360deg)} 	
	}
	
	@keyframes enter{
	 0%{transform: scale(1) } 
	 60%{transform: scale(1.1)} 
	 100%{transform: scale(1)} 	
	}
 .mini_play
  position: fixed
  display: flex
  align-items: center
  left: 0px
  bottom: 0px
  width: 100%
  height: 60px
  background: $color-highlight-background
  .avater
   flex: 0 0 40px
   padding-left: 20px
   img
    border-radius: 50%
   .avatar_move
    animation: move 20s linear infinite 
   .avatar_pause
    animation-play-state: paused      
  .info
   flex: 1
   margin-left: 4px
   .song_name
    font-size: $font-size-medium
   .singer_name
    margin-top: 5px
   	font-size: $font-size-small
   	color: $color-text-d
  .min_btns
   padding-right: 20px	
   flex: 0 0 120px
   text-align: right
   color: $color-theme-d 
   .icon-mini
   	font-size: 32px
   	margin-right: 10px
   .icon-playlist
   	font-size: 30px		 	  	
   			 	
	 
	 
	   		 
</style>


