export const Song = class {
	constructor(singer, songName, songMid, songId, albumName, albumMid, interval) {
		this.singerName = getSingerName(singer)
		this.singerMid = singer[0].mid
		this.songId = songId
		this.songName = songName
		this.songMid = songMid
		this.albumName = albumName
		this.albumMid = albumMid
		this.interval = interval
		this.songImg = getImgUrl(albumMid)
		// this.url = url
	}
}



export function createSong(song) {
	let {singer, songname, songmid, songid, albumname, albummid, interval} = song
   return new Song(singer, songname, songmid, songid, albumname, albummid, interval)	
	
}

function getSingerName(singerName) {
	let nameStr = []
	singerName.map(item => {
		nameStr.push(item.name)
	})
	return nameStr.join('-')
}

function getImgUrl(albumMid) {

	return `https://y.gtimg.cn/music/photo_new/T002R300x300M000${albumMid}.jpg?max_age=2592000`
}

export function throttle(fn) {
	let timer
	return function() {
		clearTimeout(timer)
		timer = setTimeout(fn, 200)
	}
}


