import storage from 'good-storage'
export default {
		loveHistory: getLoveHis(),
		recentlyHistory: getrecentlyHis(),
		singerName: '',
		songName: '',
		songMid: '',
		songId: -1,
		lyrics: '',
		albumName: '',
		albumMid: '',
		interval: -1,
		songImg: '',
		url: '',
		currentIndex: -1,
		sequenceList: [],
		playList: [],
		playModel: 0, 
		fullPlay: false,
		playStatus: false,
		currentTime: -1
	
}

function getLoveHis() {
	return storage.get('favorite', [])
}

function getrecentlyHis() {
	return storage.get('recently', [])
}


