<template>
<transition name="move">	
<div class="add_song" v-show="addSongShow">
	<div class="top">
		<span class="text"></span>
		<span class="incon_box" @click="_hide">
			<i class="icon-delete"></i>
		</span>
	</div>
	<div class="search_box_wrapper">
		<SearchBox text="" ref="SearchBox"  @query="_query"></SearchBox>
	</div>
	<div class="header">
		<div class="choose" v-show="!queryList.length">
			<div class="like" @click="_goLove" :class="{active: currentText === 'love'}">
				
			</div>
			<div class="recnetly" @click="_goRecnetly" :class="{active: currentText === 'recnetly'}">
				
			</div>
		</div>
	</div>
	<div class="scroll_wrapper" v-if="!queryList.length">
	<Scroll class="content" ref="loveScroll" :watcher="get_love_history" v-if="currentText === 'love'">
		<SongList 
		@palyMusic="_addSong"
		:songList="get_love_history"
		v-show="currentText === 'love'"
		></SongList>
	</Scroll>
	<Scroll class="content" :watcher="get_recently_history" v-if="currentText === 'recnetly'">
		<SongList 
		@palyMusic="_addSong"
		:songList="get_recently_history"
		v-show="currentText === 'recnetly'"
		></SongList>
	</Scroll>
	</div>	
	<Scroll class="query_list" :watcher="queryList" 
	:pullUpLoad="true" @pullUpLoad="_pullUpLoad">
	<ul class="query_list_wrapper">
		<li class="query" 
		  @click="_originAddSong(query)"	
		  v-for ="(query, index) in queryList"
		 >
			<div class="icon_wrapper">
				<i class="icon-music"></i>
			</div>
			<div class="name">
				{{query.songname}}
			</div>
			
		</li>	
	</ul>
</Scroll>
<transition name="alert">	
 <div class="alert_wrapper" v-show="alertShow">
 	<MyAlert></MyAlert>
 </div>
</transition>
</div>
</transition>
</template>	
<script type="text/javascript">
import SongList from '@/base/songList/songList'
import QueryList from '@/base/queryList/queryList'
import MyAlert from '@/base/alert/alert'
import storage from 'good-storage'
import Scroll from '@/base/scroll/scroll'
import SearchBox from '@/base/searchBox/searchBox'
import { mapGetters, mapMutations, mapActions} from 'vuex'	
import { random } from '@/common/js/random'
import { Base64 } from 'js-base64'	
import {getplaysongvkey, getLyrics} from '@/api/singer'
import { createSong } from '@/common/js/search.js'
import { ERR_OK } from '@/api/config'
import { setRecently, randomPlay } from '@/common/js/mixin'
import { getQueryResult } from '@/api/search.js'

	export default {
		mixins: [setRecently, randomPlay],
		components: {
			SongList,
			Scroll,
			SearchBox,
			QueryList,
			MyAlert
		},
		data() {
			return {
				songList: [],
				recnetlySongList: [],
				currentText: 'love',
				addSongShow: false,
				queryList: [],
				queryText: '',
				queryRusultCurNum: 1,
				alertShow: false
			}
		},
		computed: {
			...mapGetters([
				'get_play_list',
				'get_recently_history',
				'get_love_history',
				'get_current_index'
				])
		},
		methods: {
			_addSong(song) {
				console.log(song)
				let songList = this.get_play_list
				let index = this.get_current_index
				let findIndex = songList.findIndex(item => item.songMid === song.songMid)
				if(findIndex >= 0) {
					songList.splice( findIndex, 1)
				}
				songList.unshift(song)
				this.SET_PLAY_LIST(songList)
				this.SET_SEQUENCE_LIST(songList)
				this.SET_CURRENT_INDEX(++index)
				this.alertShow = true
				clearTimeout(this.timer)
				this.timer = setTimeout(() => {
					this.alertShow = false
				}, 1500)
			},
			_originAddSong(song) {
				this._addSong(createSong(song))
			},
			_pullUpLoad() {
				if(this.queryList[0].songTotalNum<=this.queryList.length) {
					return
				}
				let query = this.$refs.SearchBox.getQuery()
				this.queryRusultCurNum++
				this._getQueryResult(query, this.queryRusultCurNum)

				console.log(this.queryList)
			},
			_goLove() {
				this.currentText = 'love'
			},
			_goRecnetly() {
				this.currentText = 'recnetly'
			},
			_hide() {
				this.addSongShow = false
			},
			show() {
				this.addSongShow = true
			},
			_query(query) {
				this.queryText = query
				if(query === ''){
					return this.queryList = []
				}

				this.queryList = []
				this.queryRusultCurNum = 1
				this._getQueryResult(query, this.queryRusultCurNum)

			},
			_getQueryResult(query, curNum) {
				getQueryResult(query, curNum).then(res => {
					// this.queryList = []
					if(res.code === ERR_OK) {
					 let songTotalNum = res.data.song.totalnum
					  res.data.song.list.map(item => {
					  	item.songTotalNum = songTotalNum
					  	this.queryList.push(item)
					  })
					  console.log(this.queryList)
					}
					
				})
			},
			refresh() {
				this.$refs.loveScroll.refresh()
			},
			...mapMutations([
				'SET_LYRICS',
				'SET_LYRICS',
				'SET_URL',
				'SET_PLAY_LIST',
				'SET_SEQUENCE_LIST',
				'SET_CURRENT_INDEX'
				]),
			...mapActions([
				'setSongInfo'
				])
		}

	}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus" >
  @import '~@/common/stylus/variable.styl'
    .move-enter, .move-leave-to
     transform: translate3d(100%, 0, 0)
    .move-enter-active, .move-leave-active
     transition: all 0.3s	 
	.add_song
	 position fixed	
	 width 100%
	 height 100%
	 // background $color-background 
	 background $color-background 
	 z-index 1000
	 left 0px
	 top: 0px
	 .top
	  position relative	
	  height 44px	 
	  text-align center
	  line-height 44px
	  .incon_box
	   position absolute
	   right: 15px
	   padding 0px 5px 5px 5px 	
	   top: 0
	   font-size 20px
	   color: $color-theme
	 .search_box_wrapper
	  margin: 0 20px	  
	  padding 20px 0
	 .header
	  width 100%
	  .back
	   padding: 5px	
	   position absolute	
	   top: 10px
	   color: $color-theme
	   font-size 25px
	   left 10px
	  .choose
	   width 50%
	   margin 10px auto
	   font-size 0px
	   border-radius 5px
	   overflow hidden
	   .like, .recnetly
	   	display inline-block
	   	box-sizing border-box
	   	padding 10px
	   	width 50%
	    text-align center
	    font-size $font-size-medium
	    // background $color-highlight-background
	    background $color-background-d 	
	   .active
	   	background $color-highlight-background
	   	// background $color-background-d 
	  .rand_wrapper
	   width 30%	 	
	   margin: 20px auto
	   padding: 10px 15px
	   border: 1px solid $color-highlight-background
	   border-radius: 20px
	   text-align center 
	   font-size $font-size-small
	   color: $color-text-l
	   .icon
	    vertical-align middle
	    margin-right 5px
	   	font-size $font-size-medium-x 
	.scroll_wrapper   	
	 .content
	  overflow hidden	
	  height calc(100vh - 162px)
	.query_list
	 height: calc(100vh - 128px)	
	 overflow hidden
	 padding: 0 10px
	 .query_list_wrapper
	  .query	
	   display flex
	   padding-bottom 20px
	   color: $color-text-d
	   font-size $font-size-medium
	   overflow hidden
	  .icon_wrapper
	   flex: 0 0 40px
	   text-align center
	   .inco-music
	    font-size 28px
	  .name
	   flex: 1
	   white-space nowrap
	   overflow hidden
	   text-overflow ellipsis
	.alert-enter, .alert-leave-to
	 transform: translate3d(0, -100%, 0)  
	.alert-enter-active, .alert-leave-active
	 transition: all 0.3s	 
	.alert_wrapper
	 width 100%	
	 position absolute	   
	 top: 0px
</style>


