<template>
<Scroll class="rank" :watcher="topList">
	<ul class="rank_wrapper" ref="rank_wrapper">
		<li class="songs" v-for="item in topList" @click="_getTopListDetailByid(item.topId)">
			<div class="avatar">
				<img :src="item.frontPicUrl" width="100" alt="">
			</div>
			<ul class="content">
				<li class="song" v-for="(song, index) in item.song" >
						<span class="song_name" v-if="index <= 2">
							{{index + 1}}
						</span><span class="singer_name" v-if="index <= 2">
							{{song.title}} -{{song.singerName}}
						</span>
						
				</li>
			</ul>
		</li>
	</ul>
	<transition name="move">
 	  <router-view :songsListByMid = "songsListByMid" :rank="true" v-show="songsListByMid.length"></router-view>
 	</transition>
 	<div class="loading_wrapper" v-show="!topList.length">		
 		<Loading></Loading>
 	</div>
</Scroll>
</template>	
<script type="text/javascript">
import { getTopList, getTopListDetailByid }	from '@/api/rank'
import { createToplist } from '@/common/js/rank'
import { createSong } from '@/common/js/rank.js'
import Scroll from '@/base/scroll/scroll'
import Loading from '@/base/loading/loading'
import { mapGetters, mapMutations, mapActions} from 'vuex'
import { toggleBottom } from '@/common/js/mixin'


export default {
	mixins:[toggleBottom],
	components: {
		Scroll,
		Loading
	},
	data() {
		return {
			topList: [],
			songsListByMid: []
		}
	},
	created() {
		this._getTopList()
	},

	computed: {
	...mapGetters([
		'get_current_index'
		])
	},

	methods: {
		_getTopList() {
			getTopList().then(res => {
				res.data.group.map(group => {
					group.toplist.map(item => {
						this.topList.push(createToplist(item))
					})
				})
			})
			console.log(this.topList)
		},
		// _getTopListDetailByid(topId) {
		// 	getTopListDetailByid(topId).then(res => {
		// 		console.log(res)
		// 	})
		// },
		_getTopListDetailByid(topId) {
	 	console.log(topId)
	 	getTopListDetailByid(topId).then(res => {
	 		console.log(res)
	 		this.songsListByMid = []
	 		res.map(item => {
	 			this.songsListByMid.push(createSong(item))
	 		})
	 		console.log(this.songsListByMid)
	 		this.$router.push(`./rank/${topId}`)
	 	})
	 },
		_rankHeight() {
			return `${window.innerHeight - 65}px`
		},


		toggleBottom() {
		 	if (this.get_current_index === -1) {
		 		this.$refs.rank_wrapper.style.paddingBottom = '0px'
		 	}else {
		 		this.$refs.rank_wrapper.style.paddingBottom = '60px'
		 	}
		 }
	}
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
 @import '~@/common/stylus/variable.styl'
 .rank
  height: calc(100vh - 85px)		
	.rank_wrapper 
	 padding: 0 10px	
	 .songs
	  display: flex
	  margin-top: 20px
	  background: $color-highlight-background		
	  .avatar
	   flex: 0 0 100px
	   height: 100px
	  .content
	   flex: 1
	   display: flex
	   flex-flow: column
	   // width: 100%
	   justify-content: center
	   item-align: center
	   padding:10px 20px
	   // box-sizing: border-box
	   overflow: hidden
	   
	   .song
	   	flex: 1
	   	// display: flex
	   	align-items: center
	   	white-space: nowrap
	   	overflow: hidden
	   	line-height: 26px
	   	text-overflow: ellipsis
	   	color: $color-text-d 	
	   	// font-size: 0px
	   	.song_name, .singer_name
	   	 color: $color-text-d 	
	   	 font-size: $font-size-small-s
	.loading_wrapper
	 position absolute
	 left: 50%
	 transform translateX(-50%)	   	 
	 top: 200px
	   	 
	 
</style>


