<template>
<div class="lyrics_top">
<Scroll 
 ref="scroll"
 :watcher="lyricsLines" 
 class="lyrics" 
 :style="{height: 
 _lyricsHeight()}">
	<div class="lyrics_wrapper" v-show="lyricsLines.length">
		<p class="text" ref="text" v-for="(item, index) in lyricsLines" :class="{ active: lyricsLinesNum === index}" >
			{{item.txt}}
		</p>
	</div>
</Scroll>
</div>	
</template>	
<script type="text/javascript">
import { mapGetters } from 'vuex'
import Lyric from 'lyric-parser'
import { Base64 } from 'js-base64'
import Scroll from '@/base/scroll/scroll.vue'
	export default {
		components: {
			Scroll
		},

		data() {
			return {
				lyrics: [],
				lyricsLinesNum: -1,
				lyricsLines: []

			}
		},
		mounted() {
			this._initLtrics()
			this.get_play_status ? this._play() : this._stop()
		},
	
		watch: {
			get_lyrics: {
				handler(newVal) {
						this._initLtrics()
						this.$refs.scroll.refresh()		
				},
				deep: true
			},

			get_play_status(newVal) {
				// newVal ? this._play() : this._stop()
				this._togglePlay()
			},

			get_current_time(newVal) {
				console.log(111111)
				this._seek(newVal*1000)
			}
		},
		methods: {
			_lyricsHeight() {
				return `${window.innerHeight - 80 -130 - 50}px `
			},
			_initLtrics() {
				this.lyrics = new Lyric(this.get_lyrics, this._lyricHandler)
				this.lyricsLines.splice(0, this.lyricsLines.length)
				this.lyrics.lines.map(item => this.lyricsLines.push(item))
				console.log(this.lyricsLines)
				
				
			},
			_lyricHandler({lineNum, txt}) {
				// this._seek(this.lyricsLinesNum)
				this.lyricsLinesNum = lineNum
				let itemEls = this.$refs.text
				if(lineNum <= 5) {
					this.$refs.scroll && this.$refs.scroll.scrollTo(0, 0, 0)
				}else {
					this.$refs.scroll && this.$refs.scroll.scrollToEL(itemEls[lineNum-4], 300)
				}
				
			},

			_play() {
			 	this.lyrics.play()	
			},

			_stop() {
				 this.lyrics.stop()	
			},
			_seek(starTime) {
				this.lyrics.seek(starTime)
			},
			_togglePlay() {
				this.lyrics.togglePlay()
			},
			_seek(startTime) {
				this.lyrics.seek(startTime)
			}
		},
		computed: {
			
			...mapGetters([
				'get_lyrics',
				'get_play_status',
				'get_current_time'
				])
		}
	}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~@/common/stylus/variable.styl'
.lyrics
 box-sizing: border-box	
 overflow: hidden			
 .lyrics_wrapper
  padingg-bottom: 80px
  width: 80%
  margin: 0 auto
  .text
   line-height: 32px		
   text-align: center
   font-size: $font-size-medium
   color: $color-text-l
  .active
   color: $color-text 
   	  		




</style>


