export function random(arr) {
	arr = arr || []
	let arrLength = arr.length
	for(let i = 0; i < arrLength; i++) {
		 let rand = randomNum(0, i)
		 let t = arr[i]
		 arr[i] = arr[rand] 
		 arr[rand] = t

		 
	}
	return arr
}

function randomNum(min, max) {
	return Math.floor(Math.random()*max + min)
}


