<template>
<div class="singer_top">
<Scroll class="singer" ref="singerScroll" :watcher="singersList" :probeType="proNum" @scroll="_getToggleActive">
	<div class="singer_warpper" ref="singer_warpper">
		<div class="content">
			<div class="singers" ref="singers" v-for="singers in singersObj" v-if="singersObj">
				<div class="item_title">
						{{singers.name}}
				</div>
				<div 
				@click="_getSingerById(singer.Fsinger_mid)"
				class="singer_item" 
				v-for="singer in singers.list">
					
					<div class="avatar">
						<img v-lazy="singer.Fsinger_img" width="50" height="50" alt="">
					</div>
					<div class="name">
						{{singer.Fsinger_name}}
					</div>
				</div>
			</div>
		</div>
	</div>
	<div class="min_wrapper">
		<div 
		ref="min_item"
		 class="min_item"
		 :class="{active: minIndex === index}" 
		 :data-key="singers.name"
		 @click="_followFixedMove(index)"
		 @touchstart.stop.prevent="_touchstart(index, $event)"
		 @touchmove.stop.prevent="_touchmove"
		 @touchend.stop.prevent="_touchend"
		 v-for="(singers, key, index) in singersObj">
			{{singers.name.substring(0, 1)}}
			
		</div>
	</div>
	<!-- <div class="list_by_singer_id">
		<singer-by-id :songsListByMid="songsListByMid" v-if="songsListByMid.length"></singer-by-id>
	</div> -->
	
	<div class="fixed_title" ref="fixed_title">
		{{fixedText}}
	</div>
	<div class="loading_wrapper" v-show="!singersObj">
 		<Loading></Loading>
 	</div>
</Scroll>
<transition name="move">
	<router-view :songsListByMid="songsListByMid"></router-view>
</transition>
</div>	
</template>	
<script type="text/javascript">
import Loading from '@/base/loading/loading'	
import singerById from '@/components/singerById/singerById'
import MusicPlay from '@/components/musicPlay/musicPlay'	
import {getSingersList, getSingerById, getSingers}	from '@/api/singer'
import {createSinger, createSong} from '@/common/js/singer'
import {formatToJson} from '@/common/js/formatToJson'
import Scroll from '@/base/scroll/scroll'
import { mapGetters, mapMutations, mapActions} from 'vuex'	
import { toggleBottom } from '@/common/js/mixin'
const FIXED_HEIGHT = 30
	export default {
		mixins:[toggleBottom],
		components: {
			Scroll,
			Loading,
			singerById,
			MusicPlay
		},

		data() {
			return {
				singersList: [],
				singersObj: null,
				singerHeightArr: [],
				songsListByMid: [],
				fixedText: '',
				proNum: 3,
				minIndex: 0,
				clickDialong: false
			}
		},

		created() {
			this.touch = {
				inited: false
			}
			window.addEventListener('load', ()=>{
				console.log(1111111)
				this.$router.push('/singer')
			})
				this._getSingersList()

			
		},
		computed: {
		...mapGetters([
			'get_current_index'
			])
		},
		watch: {
			fixedText() {
				this.$nextTick(() => {
					this.$refs.fixed_title.style.transform = `translate3D(0, 0, 0)`
				}) 
			}
		

		},
		methods: {
			hidClickDia(val) {
				this.clickDialong = val
			},
			_followFixedMove(index) {
				let targetEl = this.$refs.singers[index]
				this.$refs.singerScroll.scrollToEL(targetEl)
			},
			_touchstart(index, $event) {
				let firstPo = $event.changedTouches[0]
				let startY = Math.floor(firstPo.clientY)
				this.touch.inited = true
				this.touch.startY = startY
				// console.log(index)
				// console.log($event)
				this.minIndex = index
				this.touch.index = index
				this._followFixedMove(index)
				
			},
			_touchmove($event) {
				let scrollBool = this.touch.inited
				let firstPo = $event.changedTouches[0]
				let startY = Math.floor(firstPo.clientY)
				let offsetHeight = this.$refs.min_item[0].offsetHeight 
				if(!scrollBool) {
					return
				}
				let dirNum = (startY - this.touch.startY)/offsetHeight
				dirNum = dirNum > 0 ? Math.floor(dirNum) : Math.ceil(dirNum)
				console.log(startY)
				this.minIndex = this.minIndex > this.touch.index ? Math.min(this.touch.index + dirNum, 22) : Math.max(0, this.touch.index + dirNum)
				this._followFixedMove(this.minIndex)
				// console.log(this.minIndex)



			},
			_touchend() {
				this.touch.inited = false
			},
		 	_getSingersList() {
		 		getSingersList().then(res => {
		 			// console.log(res)
		 			let originSingerData = formatToJson(res)
		 			originSingerData.map(singer => {
		 				let temObj = {}
		 				temObj.Fsinger_name = singer.Fsinger_name
		 				temObj.Fsinger_mid = singer.Fsinger_mid
		 				temObj.Findex = singer.Findex
		 				temObj.Fsinger_img = `https://y.gtimg.cn/music/photo_new/T001R300x300M000${temObj.Fsinger_mid}.jpg?max_age=2592000`
		 				this.singersList.push(temObj)
		 			})
		 			this._formatSingerList(this.singersList)
		 		})
		 	},
		 	_formatSingerList(singerList) {
		 		let singersObj = {}
		 		singersObj.hot = {
		 			name: '',
		 			list: singerList.slice(0, 10)
		 		}
		 		let temSingerList = singerList.slice(0)

		 		temSingerList.sort((a,b) => {
		 			return a.Findex.charCodeAt() - b.Findex.charCodeAt()
		 		})
		 		temSingerList.shift()
		 		temSingerList.map(item => {
		 			if(!singersObj[item.Findex]) {
		 				singersObj[item.Findex] = {
		 					name: item.Findex,
		 					list: [{
		 					Fsinger_name: item.Fsinger_name,
		 					Fsinger_mid: item.Fsinger_mid,
		 					Findex: item.Findex,
		 					Fsinger_img: item.Fsinger_img
		 				}]
		 				}
		 			}else {
		 				singersObj[item.Findex].list.push({
		 					Fsinger_name: item.Fsinger_name,
		 					Fsinger_mid: item.Fsinger_mid,
		 					Findex: item.Findex,
		 					Fsinger_img: item.Fsinger_img
		 				})
		 			}
		 		})
		 		this.singersObj = singersObj
		 		console.log(singersObj)
		 		this._getSingersHeight()
		 	}, 
		 	_getSingersHeight() {
		 		let heightArr = []
		 		this.$nextTick(() => {
		 			let singers =this.$refs.singers
		 			singers.map(item => {
		 				heightArr.push(item.offsetHeight)
		 			})
		 			heightArr.unshift(0)
		 			this.singerHeightArr = heightArr
		 			console.log(this.singerHeightArr)
		 		
		 		})
		 	},
		 	_getToggleActive(posY) {
		 		
		 		let height = 0
		 		let oriPosy = posY
		 		// console.log(oriPosy)
		 		this.singerHeightArr.map((offsetH, index) => {
		 			height += offsetH
		 			posY = Math.floor(Math.abs(posY))
		 			let nextHeight = height +this.singerHeightArr[index + 1]
		 			if(posY >= height &&  posY< nextHeight) {
		 				// console.log(index)
		 					this.minIndex = index
		 					let itemKey =this.$refs.min_item[index].getAttribute('data-key')
		 					this.fixedText = itemKey
		 					if(Math.abs(nextHeight-posY) <= FIXED_HEIGHT) {
		 						let disMoveY = -(FIXED_HEIGHT-(nextHeight-posY))
		 						// disMoveY = disMoveY < -(FIXED_HEIGHT-2) ? 0 : disMoveY
		 						this.$refs.fixed_title.style.transform = `translate3D(0, ${disMoveY}px, 0)`
		 					} else if(oriPosy > 0) {
		 						this.$refs.fixed_title.style.display = 'none'
		 					} else {
		 						this.$refs.fixed_title.style.display = 'block'
		 						// this.$refs.fixed_title.style.transform = `translate3D(0, 0px, 0)`
		 					}
		 					
		 					
		 				
		 			}

		 		})
		 	},
		 	_getSingerById(singerByMid) {
		 		getSingerById(singerByMid).then(res => {
		 				console.log(res.data.list)
		 				this.songsListByMid.splice(0, this.songsListByMid.length)
		 				res.data.list.map(song => {
		 					this.songsListByMid.push(createSong(song.musicData)) 
		 			})
		 				console.log(this.songsListByMid)
		 			 // this.$refs.singerById.showClickDia()
		 			 this.clickDialong = true
		 			 this.$router.push(`/singer/${singerByMid}`)
		 		})
		 	},
		 	toggleBottom() {
			 	if (this.get_current_index === -1) {
			 		this.$refs.singer_warpper.style.paddingBottom = '0px'
			 	}else {
			 		this.$refs.singer_warpper.style.paddingBottom = '60px'
			 	}
			 }
		}
	}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
 @import '~@/common/stylus/variable.styl'
   .move-enter, .move-leave-to
   	// opacity: 0
   .move-enter-active, .move-leave-active
   	transition: all 0.3s	
   .singer
   	position: absolute
    width: 100%
    height: 100%
    overflow: hidden
    // z-index: 100
    margin-top: 64px	
    left: 0px 	
    top: 0px	
	.singer_warpper
	 &::after
	  content ''
	  display: block			
	  height: 64px
	 .content
	  // margin-top: 10px	
	  .singers
	   .item_title
	   	width: 100%
	   	box-sizing: border-box
	   	padding-left: 20px
	   	height: 30px
	   	line-height: 30px
	   	background: $color-highlight-background
	   	font-size: $font-size-small
	   	color: $color-text-l	
	   .singer_item
	   	padding-left: 30px
	   	margin-top: 20px
	   	display: flex
	   	font-size: 0
	   	// padding-bottom: 20px	
	   	&:last-child
	   	 padding-bottom: 20px 		 
	   	.avatar
	   	 flex: 0 0 50px	
	   	 border-radius: 50%
	   	 overflow: hidden
	   	 margin-right: 18px
	   	.name
	   	 flex: 1
	   	 line-height: 50px
	   	 font-size: $font-size-medium  	 
	   	 color: $color-text-l  	 
	.min_wrapper
	 position: fixed
	 z-index: 1
	 top: 50%
	 margin-top: 40px
	 transform: translateY(-50%)
	 // top: 85px
	 right: 0px
	 background: $color-background-d	  	
	 width: 20px
	 font-size: $font-size-small
	 padding: 20px 0px
	 border-radius: 15px
	 .min_item
	  text-align: center
	  padding: 3px 0px
	  color: $color-text-l
	 .active  
	  color: $color-theme
	.fixed_title
	 position: fixed
	 top: 64px
	 left: 0px	
	 width: 100%
	 box-sizing: border-box
	 padding-left: 20px
	 height: 30px
	 line-height: 30px
	 background: $color-highlight-background
	 // background: red
	 // opacity: 0.5
	 font-size: $font-size-small
	 color: $color-text-l
	.loading_wrapper
	 position: absolute
	 width: 100%
	 top: 200px	 	
	 	   		
</style>


