import * as types from './types'
export default {
	[types.SET_SINGER_NAME](state, newVal) { state.singerName = newVal },

	[types.SET_SONG_NAME](state, newVal) { state.songName = newVal },

	[types.SET_SONG_MID](state, newVal) { state.songMid = newVal },

	[types.SET_SONG_ID](state, newVal) { state.songId = newVal },

	[types.SET_LYRICS](state, newVal) { state.lyrics = newVal },

	[types.SET_ALBUM_NAME](state, newVal) { state.albumName = newVal },

	[types.SET_ALBUM_MID](state, newVal) { state.albumMid = newVal },

	[types.SET_INTERVAL](state, newVal) { state.interval = newVal },

	[types.SET_SONG_IMG](state, newVal) { state.songImg = newVal },

	[types.SET_URL](state, newVal) { state.url = newVal },

	[types.SET_CURRENT_INDEX](state, newVal) { state.currentIndex = newVal },

	[types.SET_SEQUENCE_LIST](state, newVal) {state.sequenceList = newVal}, 

	[types.SET_PLAY_LIST](state, newVal) {state.playList = newVal}, 
	
	[types.SET_PPLAY_MODEL](state, newVal) {state.playModel = newVal},

	[types.SET_FULL_PLAY](state, newVal) {state.fullPlay = newVal},

	[types.SET_PLAY_STATUS](state, newVal) {state.playStatus = newVal},

	[types.SET_CURRENT_TIME](state, newVal) {state.currentTime = newVal},

	[types.SET_LOVE_HISTORY](state, newVal) {state.loveHistory = newVal},

	[types.SET_RECENTLY_HISTORY](state, newVal) {state.recentlyHistory = newVal}
		
}




