<template>
<transition name="menu">	
<div class="songs_view" v-show="songsMenuShow">
	<div class="songs_views_wrapper">
		<div class="play_btns">
			<div class="play_modes" @click="_togglePlayMode">
				<i class="icon" :class="playModeClass()"></i><span class="text"></span>
			</div>
			<div class="delete_wrapper" @click="_clear">
				<i class="icon icon-clear"></i>
			</div>
		</div>
		<Scroll class="songs_wrapper" ref="scroll" :watcher="get_play_list">
		<ul class="songs" >
			<transition-group name="delete" tag="div">	
			<li 
			ref="song"
			:data-key="index"
			:key ="index"	
			class="song" 
			v-for="(song, index) in get_play_list">
				<div class="name" @click="_playMusic(song)"><i class="icon" :class="{'icon-play' : get_current_index === index}"></i><span class="text">{{song.songName}}</span></div>
				<div class="right_btns">
					<i 
					:class="_getFavoriteClass(song)"
					@click="_toggleFavorite(song, index)"
					ref="favorite"
					></i>
					<i class="icon-delete" @click.stop="_deleteSong(index)"></i>
				</div>
			</li>
		</transition-group>
		</ul>
		</Scroll>
		<div class="close_wrapper" @click="toggleShow">
		<span></span>
		</div>
		<div class="addSongs" @click="_showAddsong">
			<i class="icon-add"></i><span></span>
		</div>
	</div>
	<div class="dialong" @click.stop="toggleShow"></div>
	<div class="confirm_wrapper">
		<Confirm @okey="_okey" ref="confirm"></Confirm>
	</div>
	<AddSongList ref="addSongList"></AddSongList>
</div>
</transition>
</template>	
<script type="text/javascript">

 import { mapGetters, mapMutations, mapActions } from 'vuex'	
 import Scroll from '@/base/scroll/scroll'
 import Confirm from '@/base/confirm/confirm'
 import { random } from '@/common/js/random'
 import storage from 'good-storage'
 import { setStorage, getStorage } from '@/common/js/localStorage.js'
 import { Base64 } from 'js-base64'	
 import {getplaysongvkey, getLyrics} from '@/api/singer'
 import { createSong } from '@/common/js/search.js'
 import { ERR_OK } from '@/api/config'
 import { setRecently, setLocalLove, togglePlayMode } from '@/common/js/mixin'
 import AddSongList from '@/components/addSongList/addSongList'

 export default {
 	mixins: [setRecently, setLocalLove, togglePlayMode],
 	components: {
 		Scroll,
 		Confirm,
 		AddSongList
 	},
 	data() {
 		return {
 			songsMenuShow: false,
 			currentIndex: 0
 		}
 	},
 	computed: {
 		...mapGetters([
 			'get_play_list',
 			'get_current_index',
 			'get_play_model',
 			'get_sequence_list'


 			])
 	},
 	watch: {
 		get_current_index(newVal){
 		   console.log('typeof', typeof newVal)
 		   this.$nextTick(()=> {
 		   		this._listMove()
 		   })
 		   
 		}
 	},
 	methods: {
 		toggleShow() {

 			this.songsMenuShow = !this.songsMenuShow
 			if(this.songsMenuShow) {
 				this.$nextTick(() => {
 					this.$refs.scroll.refresh()
 					this._listMove()

 				})
 				
 			}


 		},
 		_deleteSong(index) {
 			let tepArr = this.get_play_list.slice()
 			tepArr.splice(index, 1)
 			let curIndex = this.get_current_index
 			

 			

 			if(!tepArr.length) {
 				this._hide()
 			} 

 			if(curIndex === index) {
 				console.log(100000)
 				curIndex = curIndex === this.get_play_list.length-1 ? 0 : curIndex + 1
 				console.log('curIndex', curIndex)
 				this._playMusic(this.get_play_list[curIndex])
 			}

 			if(curIndex > index) {
 				this.SET_CURRENT_INDEX(--curIndex)
 			}

 			this.SET_PLAY_LIST(tepArr)

 		},
 		_okey() {
 			this.SET_PLAY_LIST([])
 			this._hide()
 		},
 		_clear() {
 			console.log(this.$refs.confirm)
 			this.$refs.confirm.show()
 		},
 		_hide() {
 			this.SET_CURRENT_INDEX(-1)
 			this.SET_PLAY_STATUS(false)
 			this.songsMenuShow =false
 		},
			_playMusic(song) {
				this._getLyrics(song.songMid, (res) => {
					let formatStr = Base64.decode(res) 
					this.SET_LYRICS(formatStr)
				})
				let index = this.get_play_list.findIndex(item => song.songMid === item.songMid)
				this.singerMid = song.singerMid
				song.currentIndex = index
				song.fullPlay = false
				song.playStatus = true
				song.lyrics = ''
				this.playMusicData = {...song}
				console.log(song)
				getplaysongvkey(song.songMid).then(res => {
					let url ='http://dl.stream.qqmusic.qq.com/' + res
					this.SET_URL(url)
					
				}) 
				song.url = ''
				this.changeList(song)
				this._setRecently(song)
				this.SET_RECENTLY_HISTORY(storage.get('recently'))
			},
			_getLyrics(singId, cameBack) {
				getLyrics(singId).then(res => {
					if(res.code === ERR_OK) {
						cameBack(res.lyric)
					}
				})
			}, 
			_listMove() {
				let index = this.get_current_index
	 		   	if(this.get_play_model < 2) {
	 		   		index = this.get_sequence_list.findIndex(item => this.get_play_list[this.get_current_index].songMid === item.songMid)		
	 		   	}
	 		   	index = index >= 4 ? index - 2 : index
	 			let el = this.$refs.song[index]
	 			this.$refs.scroll.scrollToEL(el, 300)
				},
	 			playModeClass() {
					return this.get_play_model === 0 ? 'icon-sequence' : this.get_play_model === 1 ? 'icon-loop' : 'icon-random' 
			},
			_showAddsong() {
				this.$refs.addSongList.show()
				this.$nextTick(() => {
					this.$refs.addSongList.refresh()
				})
				
			},
 		...mapMutations([
 			'SET_PLAY_LIST',
 			'SET_PPLAY_MODEL',
 			'SET_CURRENT_INDEX',
 			'SET_PLAY_STATUS',
 			'SET_FULL_PLAY',
 			'SET_LYRICS',
 			'SET_URL',
 			'SET_RECENTLY_HISTORY',
 			'SET_LOVE_HISTORY'
 			]),
 		...mapActions([
 			'changeList'
 			])
 	}
 }
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
 @import '~@/common/stylus/variable.styl'
    .menu-enter, .menu-leave-to
     transform: translate3d(0, 100%, 0)
     // opacity 0	
    .menu-enter-active, .menu-leave-active
     transition: all 0.4s	 
	.songs_view
	 position fixed
	 width 100%
	 bottom: 0px
	 left 0px
	 padding 60px 0px 140px 0px
	 max-height 260px
	 overflow: hidden
	 // box-sizing border-box
	 background $color-highlight-background
	 .songs_views_wrapper
	   padding: 0 20px
	   overflow hidden	
	  .play_btns
	   position absolute
	   display flex
	   width 100%		
	   top 0px
	   left 0px
	   height 60px
	   font-size 20px
	   .play_modes, .delete_wrapper
	   	display inline-block
	   	line-height 60px
	   	font-size: $font-size-medium
	   	color: $color-text-l
	   	flex: 1
	   	.icon
	   	 vertical-align middle
	   	 margin:0 10px 0 20px 
	   	 font-size 20px
	   	 color: $color-theme
	   	.text
	   	 vertical-align middle	 	
	   .delete_wrapper
	   	flex: 0 0 60px
	   	text-align right
	  .songs_wrapper 
	  	max-height 260px
	  	overflow hidden		
	   .songs
	   	// max-height 320px
	   	.delete-enter, .delete-leave-to
	   	 // opacity 0
	   	 
	   	 opacity 0
	   	 // transform: translate3d(0, -100%, 0)
	   	.delete-enter-active, .delete-leave-active
	   	 // position: absolute
	   	 height 0px		
	   	 // transition: height 3s
	   	 
	   	 // position: relative	
	   	.delete-move
	   	 transition: transform 0.3s	 	 		
	    .song
	     display flex
	     padding: 15px 0px
	     
	     .name
	      flex: 1
	      font-size 14px
	      color: $color-theme
	      overflow hidden
	      white-space nowrap
	      text-overflow ellipsis
	      .icon
	       display inline-block	
	       width: 28px
	      .text
	       color: $color-text-d  	
	     .right_btns
	      flex: 0 0 80px
	      text-align right
	      color: $color-theme
	      font-size 12px
	      .icon-delete
	       margin-left: 10px
	       margin-right 5px		 	
	  .close_wrapper
	   position absolute
	   width 100%	
	   height 50px
	   line-height 50px
	   text-align center
	   bottom 0px	 	
	   left 0px
	   background $color-background
	  .addSongs
	   position absolute
	   padding: 10px 10px	 
	   border-radius: 15px
	   border: 1px solid #fff
	   width 140px
	   left: 50%
	   text-align center
	   transform translateX(-50%)
	   bottom: 80px
	   font-size 12px
	   color: $color-text-l
	   .icon-add 
	    margin-right 10px
	 .dialong
	  position fixed
	  z-index -1
	  top: 0
	  left: 0
	  width 100%
	  height 100%
	  background $color-background-d
	 .confirm_wrapper
	  width: 100%		
	  position absolute	 	
	  top: 50px
	  left: 0px
	 	
</style>



