<template>
<div class="search">
	<div class="search_wrapper" >
		<div class="search_box">
			<SearchBox ref="SearchBox" @query="_query"></SearchBox>
		</div>
		<div class="hot_search" v-show="!queryList.length">
			<h2 class="hot_search_title"></h2>
			<ul class="hot_search_wrapper" >
				<li class="hot_search_item"
				 @click="_setQuery(hotItem.k)"
				 v-for = "hotItem in hotDatas">
					<span class="item">{{hotItem.k}}</span>
				</li>
			</ul>
		</div>
		<div class="history" v-show="localHistory.length && !queryList.length">
		<h2 class="title"><span></span><span class="icon" @click="_clearHis"><i class="icon-clear"></i></span></h2>
		<div class="content" >
			<span class="text" 
			   v-for="text in localHistory"
			   v-text="text"
			   @click="_setQuery(text)"	
			   ></span>
		</div>
	</div>
	</div>
	
	<div class="query_list_wrapper" v-show="queryList.length" 
		:style="{height: _getQueryWrapperHeight()}">
		<QueryList 
			:queryList = 'queryList' 
			@pullUpLoad="_pullUpLoad"
			@addHistory="_addHistory"
			></QueryList>
	</div>
	<router-view :songsListByMid="songsListByMid"></router-view>	
</div>
</template>	
<script type="text/javascript">
import SearchBox from '@/base/searchBox/searchBox'
import QueryList from '@/base/queryList/queryList'
import { getHotSearch, getQueryResult } from '@/api/search.js'
import { ERR_OK } from '@/api/config'
import storage from 'good-storage'

const search_history = '_searchHistory_'	
	export default {
		components: {
			SearchBox,
			QueryList
		},

		data() {
			return {
				hotDatas: [],
				queryList: [],
				songsListByMid: [],
				queryRusultCurNum: 1,
				localHistory: [],
				queryText: ''
			}
		},

		computed: {

		},
		created() {
			this._getHotSearch()
			this._getSeachHis()
		},
		methods: {
			_getHotSearch() {
				getHotSearch().then( res => {
					let hotKeyLen = res.length <= 10 ? res.length : 10
					for(let i = 0; i < hotKeyLen; i++) {
						this.hotDatas.push(res[i])
					}
				})
			},
			_getQueryResult(query, curNum) {
				getQueryResult(query, curNum).then(res => {
					// this.queryList = []
					if(res.code === ERR_OK) {
					 let songTotalNum = res.data.song.totalnum
					 let zhida = res.data.zhida
					 if(zhida.type) {
					 	console.log(zhida)
					 	let {singername, singermid} = zhida
					 	if(singermid) {
					 		this.queryList.push({
					 		singername,
					 		singermid,
					 		zhida: true
					 	})
					 	}
					 	
					 }

					  res.data.song.list.map(item => {
					  	item.songTotalNum = songTotalNum
					  	this.queryList.push(item)
					  })
					}
					
				})
			},
			
			_getQueryWrapperHeight() {
				return `${window.innerHeight - 105 - 40}px`
			},
			_query(query) {
				this.queryText = query
				if(query === ''){
					return this.queryList = []
				}

				this.queryList = []
				this.queryRusultCurNum = 1
				this._getQueryResult(query, this.queryRusultCurNum)

			},
			_getSeachHis() {
				this.localHistory = storage.get(search_history, [])
			},
			_addHistory() {
				if(!storage.get(search_history)) {
					if(this.queryText !== '') {
						this.localHistory.push(this.queryText)
					}
					storage.set(search_history, this.localHistory)
				}else {
					let temArr = storage.get(search_history)
					if(storage.get(search_history).length > 10) {
						temArr.pop()
						this.localHistory = temArr
					}else{ 
						this.localHistory = temArr.slice()
					}
					this.localHistory.unshift(this.queryText)
					storage.set(search_history, this.localHistory)

				}
			},

			_clearHis() {
				let temArr=storage.set(search_history, [])
				this.localHistory = temArr.slice()

			},

			_pullUpLoad() {
				if(this.queryList[0].songTotalNum<=this.queryList.length) {
					return
				}
				let query = this.$refs.SearchBox.getQuery()
				this.queryRusultCurNum++
				this._getQueryResult(query, this.queryRusultCurNum)

				console.log(this.queryList)
			},
			_setQuery(query) {
				this.$refs.SearchBox.setQuery(query)
			}
		}
	}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
 @import '~@/common/stylus/variable.styl'
 .search
  .search_wrapper
  	padding: 0 20px
   .search_box
   	margin-top: 20px
   	box-sizing: border-box
   .hot_search	
    .hot_search_title
     position relative	
   	 padding: 20px 0	 			
   	 color: $color-text-l
    .hot_search_wrapper
   	 .hot_search_item
   	  padding: 5px 8px
   	  margin: 10px 20px 0 0			
   	  border-radius: 5px
   	  display: inline-block
   	  background: $color-highlight-background
   	  color: $color-text-d 
   	  font-size: $font-size-medium
   .history
   	.title
   	 padding: 20px 0	
   	 color: $color-text-l
   	 .icon
   	  position absolute	 		  
   	  right 20px
   	.content
   	 .text
   	  padding: 5px 8px
   	  margin: 10px 10px 0 0			
   	  border-radius: 5px
   	  display: inline-block
   	  background: $color-highlight-background
   	  color: $color-text-d 
   	  font-size: $font-size-medium		 
  .query_list_wrapper
  	margin-top: 20px
  	padding: 0 20px

  	 	 
	 	
</style>


