import jsonp from '@/common/js/jsonp.js'
import axios from 'axios'

const options = {
 	param: 'jsonpCallback'
 }

export function getplaysongvkey(songmid) {
  const url = '/getplaysongvkey'
  const data = {
    g_tk:5381,
    loginUin:0,
    hostUin:0,
    format:'json',
    inCharset:'utf8',
    outCharset:'utf-8',
    notice:0,
    platform:'yqq.json',
    needNewCode:0,
    data:`{"req":{"module":"CDN.SrfCdnDispatchServer","method":"GetCdnDispatch","param":{"guid":"5090027445","calltype":0,"userip":""}},"req_0":{"module":"vkey.GetVkeyServer","method":"CgiGetVkey","param":{"guid":"5090027445","songmid":["${songmid}"],"songtype":[0],"uin":"0","loginflag":1,"platform":"20"}},"comm":{"uin":0,"format":"json","ct":24,"cv":0}}`
  }
 return axios.get(url, {
    params: data
  }).then((res) => {
   return Promise.resolve(res.data.req_0.data.midurlinfo[0].purl)
  })
}
 
export function getSingers() {
	let url = '/getSinger'
	let data = {
		_tk:5381,
		loginUin:0,
		hostUin:0,
		format:'json',
		inCharset:'utf8',
		outCharset:'utf-8',
		notice:0,
		platform:'yqq.json',
		needNewCode:0,
		data:`{"comm":{"ct":24,"cv":0},"singerList":{"module":"Music.SingerListServer","method":"get_singer_list","param":{"area":-100,"sex":-100,"genre":-100,"index":-100,"sin":0,"cur_page":1}}}`
	}

	return axios.get(url, {
	 	params: data
	 }).then(res => {
	 	return Promise.resolve(res.data.singerList.data.singerlist)
	 }).catch(err => {
	 	console.log(err)
	 })
}

export function getSingersList() {
	let url = '/getSingerData'
	let data = {
		g_tk: 1928093487,
		inCharset: 'utf-8',
		outCharset: 'utf-8',
		notice: 0,
		format: 'jsonp',
		channel: 'singer',
		page: 'list',
		key: 'all_all_all',
		pagesize: 2,
		pagenum: 1,
		hostUin: 0,
		needNewCode: 0,
		platform: 'yqq',
		jsonpCallback: 'jp0'
	}

	return axios.get(url, {
	 	params: data
	 }).then(res => {
	 	return Promise.resolve(res.data)
	 }).catch(err => {
	 	console.log(err)
	 })
	
} 

export function getSingerById(singerMid) {
	let url = 'https://c.y.qq.com/v8/fcg-bin/fcg_v8_singer_track_cp.fcg'
	let data = {
		g_tk:5381,
		loginUin:0,
		hostUin:0,
		format:'json',
		inCharset:'utf8',
		outCharset:'utf-8',
		notice:0,
		platform:'yqq',
		needNewCode:0,
		ct:24,
		singermid:`${singerMid}`,
		order:'listen',
		begin:0,
		num:100
	}
	return jsonp(url, data, options)
}

export function getLyrics(songMid) {
	let url = '/getLyrics'
	let data = {
		pcachetime:new Date().getTime(),
		songmid:songMid,
		g_tk:5381,
		loginUin:0,
		hostUin:0,
		format:'json',
		inCharset:'utf8',
		outCharset:'utf-8',
		notice:0,
		platform:'yqq.json',
		needNewCode:0
	}

	return axios.get(url,{
		params: data
	}).then(res => {
		console.log(res)
		return Promise.resolve(res.data)
	}).catch(err => {
		return Promise.reject(err)
	})
}


