<template>
<div class="scroll" ref="scroll"  @click.stop >
	<slot></slot>
</div>
</template>	
<script type="text/javascript">
import BScroll from 'better-scroll'
	export default {
		props: {
			watcher: {
				type: Array || Object
			},
			probeType: {
				type: Number,
				default: 0
			},
			scrollX: {
				type: Boolean,
				default: false
			},
			scrollY: {
				type: Boolean,
				default: true
			},
			snap: {
				type:Boolean,
				return: false
			},
			pullUpLoad: {
				type: Boolean,
				default: false
			}
		},
		created() {
			this.$nextTick(() => {
				this._initScroll() 
			}) 
			
		},

		methods: {
			_initScroll() {
				let me = this
				this.BScroll = new BScroll(this.$refs.scroll, {
					click: true,
					scrollY: this.scrollY,
					scrollX: this.scrollX,
					probeType: me.probeType ,
					snap: this.snap ? {loop: false} : false,
					pullUpLoad: this.pullUpLoad ? {  threshold: -30 }  : false
					 
					
				})
				if(this.probeType) {
					this.BScroll.on('scroll',  ({y})=> {
						this.$emit('scroll', y)
					})
				}

				if(this.pullUpLoad) {
					this.BScroll.on('pullingUp',  ()=> {
						this.$emit('pullUpLoad')
						this.BScroll.finishPullUp()
					})
				}
				
			},

			refresh() {
				this.BScroll && this.BScroll.refresh()
			},
			scrollToEL(targetEl, time) {
				this.BScroll && this.BScroll.scrollToElement(targetEl, time)
			},
			scrollTo(x, y, time, easing) {
				this.BScroll && this.BScroll.scrollTo(x, y, time, easing)
			}

		},

		watch: {
			watcher: {
				handler(newVal, oldVal) {
					console.log(10101010)
					this.$nextTick(() => {
						if(!this.BScroll ) {
						  return this._initScroll()
						}
						this.BScroll.refresh()
					})
				}
			}
		}

	}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
	
</style>


