<template>
<div class="search_box">
	<span class="icon_box"><i class="icon-search"></i></span>
	<input :placeholder="text" v-model="query" type="text">
	<span class="icon_box" @click="_clear"><i class="icon-dismiss"></i></span>
</div>
</template>	
<script type="text/javascript">
import { throttle } from '@/common/js/search.js'
	export default {
		props: {
			text:{
				type: String,
				default: ''	
			} 
			
		},

		data() {
			return {
				query: ''
			}
		},
		created() {
			this.$watch('query', throttle(()=>{
				this.$emit('query', this.query)	
			}))
		},
		methods: {
			_clear() {
				this.query = ''
			},

			setQuery(query) {
				this.query = query
			},

			getQuery() {
				return this.query
			}
		},
		watch: {
			// query(newVal) {
			// 	throttle(()=> {
			// 		console.log(111)
			// 		this.$emit('query', newVal)	
			// 	})()
				
			// }
		}
	}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
 @import '~@/common/stylus/variable.styl'
	.search_box 
	 width: 100%		
	 display: flex	
	 justify-content: center
	 height: 40px
	 border-radius: 4px
	 background: $color-highlight-background
	 .icon_box
	  flex: 0 0 40px
	  text-align center	
	  align-self: center
	  font-size: 24px
	  color: $color-background
	  .icon-dismiss
	   font-size: 16px		
	 input
	  flex: 1
	  height: 100%
	  border: none	
	  outline: none
	  background: $color-highlight-background
	  color: $color-text-ll	 

</style>


